/**
 * Music Engine
 *
 * Core music generation system using layered loops.
 * Integrates LayerManager, effects processing, and workout state.
 */

import type { MusicSettings } from '../config/settings.js';
import { LayerManager } from './layers/LayerManager.js';
import type { MusicLayer } from './layers/LayerManager.js';
import { createGainNode, createCompressor, createLowPassFilter } from './synthesis/Effects.js';

/**
 * Event types emitted by MusicEngine
 */
export type MusicEngineEvent = 'started' | 'stopped' | 'intensityChanged' | 'error';

/**
 * Event listener callback
 */
export type MusicEngineEventListener = (data?: unknown) => void;

/**
 * Core music generation engine
 */
export class MusicEngine {
    private context: AudioContext | null = null;
    private layerManager: LayerManager | null = null;
    private musicBusGain: GainNode | null = null;
    private filterNode: BiquadFilterNode | null = null;
    private compressor: DynamicsCompressorNode | null = null;
    private settings: MusicSettings;
    private eventListeners: Map<MusicEngineEvent, Set<MusicEngineEventListener>> = new Map();
    private isInitialized: boolean = false;

    constructor(settings: MusicSettings) {
        this.settings = { ...settings };
    }

    /**
     * Initialize audio context and audio graph
     */
    async initialize(): Promise<void> {
        if (this.isInitialized) {
            return;
        }

        try {
            // Create AudioContext
            const AudioContextClass = window.AudioContext || (window as any).webkitAudioContext;
            if (!AudioContextClass) {
                throw new Error('Web Audio API not supported in this browser');
            }

            this.context = new AudioContextClass();

            // Build audio graph:
            // LayerManager -> MusicBusGain -> Filter -> Compressor -> Destination
            this.musicBusGain = createGainNode(this.context, this.settings.volume);
            this.filterNode = createLowPassFilter(this.context, 8000);
            this.compressor = createCompressor(this.context);

            // Connect graph
            this.musicBusGain.connect(this.filterNode);
            this.filterNode.connect(this.compressor);
            this.compressor.connect(this.context.destination);

            // Create layer manager
            this.layerManager = new LayerManager(this.context, this.musicBusGain);

            // Register layers for initial style
            await this.setStyle(this.settings.style);

            this.isInitialized = true;
            console.log('[MusicEngine] Initialized successfully');
        } catch (error) {
            console.error('[MusicEngine] Initialization failed:', error);
            this.emit('error', error);
            throw error;
        }
    }

    /**
     * Get layer definitions for a specific style
     */
    private getLayersForStyle(style: string): MusicLayer[] {
        // Fallback to electronic if style not found
        const safeStyle = ['electronic', 'rock', 'ambient', 'minimal'].includes(style) ? style : 'electronic';

        switch (safeStyle) {
            case 'rock':
                return [
                    {
                        id: 'bass',
                        audioBuffer: null,
                        minIntensity: 0,
                        maxIntensity: 1,
                        baseVolume: 0.7,
                        type: 'bass',
                        style: 'rock',
                    },
                    {
                        id: 'drums-low',
                        audioBuffer: null,
                        minIntensity: 0.2,
                        maxIntensity: 1,
                        baseVolume: 0.6,
                        type: 'drums-low',
                        style: 'rock',
                    },
                    {
                        id: 'drums-high',
                        audioBuffer: null,
                        minIntensity: 0.6,
                        maxIntensity: 1,
                        baseVolume: 0.8,
                        type: 'drums-high',
                        style: 'rock',
                    },
                    {
                        id: 'synth',
                        audioBuffer: null,
                        minIntensity: 0.4,
                        maxIntensity: 1,
                        baseVolume: 0.5,
                        type: 'synth',
                        style: 'rock',
                    },
                ];
            case 'ambient':
                return [
                    {
                        id: 'bass',
                        audioBuffer: null,
                        minIntensity: 0,
                        maxIntensity: 1,
                        baseVolume: 0.7,
                        type: 'bass',
                        style: 'ambient',
                    },
                    {
                        id: 'drums-high',
                        audioBuffer: null,
                        minIntensity: 0.6,
                        maxIntensity: 1,
                        baseVolume: 0.5,
                        type: 'drums-high',
                        style: 'ambient',
                    },
                    {
                        id: 'synth',
                        audioBuffer: null,
                        minIntensity: 0.2,
                        maxIntensity: 1,
                        baseVolume: 0.8,
                        type: 'synth',
                        style: 'ambient',
                    },
                ];
            case 'minimal':
                return [
                    {
                        id: 'bass',
                        audioBuffer: null,
                        minIntensity: 0,
                        maxIntensity: 1,
                        baseVolume: 0.7,
                        type: 'bass',
                        style: 'minimal',
                    },
                    {
                        id: 'drums-low',
                        audioBuffer: null,
                        minIntensity: 0.3,
                        maxIntensity: 1,
                        baseVolume: 0.6,
                        type: 'drums-low',
                        style: 'minimal',
                    },
                ];
            case 'electronic':
            default:
                return [
                    {
                        id: 'bass',
                        audioBuffer: null,
                        minIntensity: 0,
                        maxIntensity: 1,
                        baseVolume: 0.7,
                        type: 'bass',
                        style: 'electronic',
                    },
                    {
                        id: 'drums-low',
                        audioBuffer: null,
                        minIntensity: 0.2,
                        maxIntensity: 1,
                        baseVolume: 0.6,
                        type: 'drums-low',
                        style: 'electronic',
                    },
                    {
                        id: 'drums-high',
                        audioBuffer: null,
                        minIntensity: 0.6,
                        maxIntensity: 1,
                        baseVolume: 0.8,
                        type: 'drums-high',
                        style: 'electronic',
                    },
                    {
                        id: 'synth',
                        audioBuffer: null,
                        minIntensity: 0.4,
                        maxIntensity: 1,
                        baseVolume: 0.5,
                        type: 'synth',
                        style: 'electronic',
                    },
                ];
        }
    }

    /**
     * Switch music style
     */
    async setStyle(style: string): Promise<void> {
        if (!this.layerManager) return;

        const wasPlaying = this.isPlaying();

        // precise transition: fade out old style if playing
        if (wasPlaying) {
            // We can't easily crossfade with current architecture without two LayerManagers
            // So we stop, load, start.
            this.layerManager.stop();
        }

        const layers = this.getLayersForStyle(style);
        this.layerManager.replaceAllLayers(layers);

        // Load new layers
        try {
            await this.layerManager.loadAllLayers();
            if (wasPlaying) {
                this.layerManager.start();
            }
        } catch (error) {
            console.error('[MusicEngine] Failed to load style layers:', error);
        }
    }

    /**
     * Trigger a buildup effect (e.g., before high intensity interval)
     * @param durationMs Duration of buildup in milliseconds
     */
    triggerBuildup(durationMs: number = 5000): void {
        if (!this.context || !this.filterNode) return;

        const now = this.context.currentTime;
        const duration = durationMs / 1000;

        // Reset/Cancel scheduled changes
        this.filterNode.frequency.cancelScheduledValues(now);

        // Sweep filter up
        // Currently frequency might be controlled by intensity, so we ramp from current?
        // Let's assume current frequency is appropriate start
        this.filterNode.frequency.setValueAtTime(this.filterNode.frequency.value, now);
        this.filterNode.frequency.exponentialRampToValueAtTime(12000, now + duration);

        // Optional: High-pass sweep on specific layers? 
        // For now, global LP filter opening up is a good "buildup"
    }

    /**
     * Trigger a cooldown/drop effect (e.g., entering recovery)
     */
    triggerCooldown(): void {
        if (!this.context || !this.filterNode) return;

        const now = this.context.currentTime;

        // Instant drop or fast sweep down
        this.filterNode.frequency.cancelScheduledValues(now);
        this.filterNode.frequency.setValueAtTime(this.filterNode.frequency.value, now);
        this.filterNode.frequency.exponentialRampToValueAtTime(400, now + 2.0);
    }



    /**
     * Start music playback
     */
    async start(): Promise<void> {
        if (!this.isInitialized) {
            await this.initialize();
        }

        if (!this.context || !this.layerManager) {
            throw new Error('MusicEngine not initialized');
        }

        // Resume AudioContext if suspended (e.g., user gesture requirement)
        if (this.context.state === 'suspended') {
            await this.context.resume();
        }

        // Load layers if not already loaded
        // Note: In production, we'd handle missing files gracefully
        try {
            await this.layerManager.loadAllLayers();
        } catch (error) {
            console.warn('[MusicEngine] Failed to load some layers:', error);
            // Continue anyway - some layers might have loaded
        }

        this.layerManager.start();
        this.emit('started');
        console.log('[MusicEngine] Started');
    }

    /**
     * Stop music playback
     */
    stop(): void {
        if (!this.layerManager) return;

        this.layerManager.stop();
        this.emit('stopped');
        console.log('[MusicEngine] Stopped');
    }

    /**
     * Update intensity (0-1) to control which layers play
     *
     * @param intensity - Intensity value (0 = minimal, 1 = maximum)
     */
    setIntensity(intensity: number): void {
        if (!this.layerManager) return;

        const clampedIntensity = Math.max(0, Math.min(1, intensity));
        this.layerManager.setIntensity(clampedIntensity);

        // Update filter cutoff based on intensity
        if (this.filterNode) {
            const minFreq = 400;
            const maxFreq = 8000;
            const targetFreq = minFreq + clampedIntensity * (maxFreq - minFreq);
            const now = this.context!.currentTime;
            this.filterNode.frequency.setTargetAtTime(targetFreq, now, 0.1);
        }

        this.emit('intensityChanged', clampedIntensity);
    }

    /**
     * Get current intensity
     */
    getIntensity(): number {
        return this.layerManager?.getIntensity() ?? 0.5;
    }

    /**
     * Set master volume
     *
     * @param volume - Volume level (0-1)
     */
    setVolume(volume: number): void {
        if (!this.musicBusGain) return;

        const clampedVolume = Math.max(0, Math.min(1, volume));
        const now = this.context!.currentTime;
        this.musicBusGain.gain.setTargetAtTime(clampedVolume, now, 0.05);
        this.settings.volume = clampedVolume;
    }

    /**
     * Duck music volume (for voice announcements)
     *
     * @param duckLevel - Level to duck to (0-1)
     * @param durationMs - Optional duration. If not provided, stays ducked until unducked.
     */
    duck(duckLevel: number = 0.2, durationMs?: number): void {
        if (!this.musicBusGain || !this.context) return;

        const now = this.context.currentTime;
        // Cancel any scheduled changes
        this.musicBusGain.gain.cancelScheduledValues(now);

        const fadeDuration = 0.2;

        // Duck down
        this.musicBusGain.gain.setValueAtTime(this.musicBusGain.gain.value, now);
        this.musicBusGain.gain.linearRampToValueAtTime(duckLevel, now + fadeDuration);

        // If duration provided, schedule restore
        if (durationMs !== undefined) {
            const restoreTime = now + fadeDuration + durationMs / 1000;
            this.forceUnduck(restoreTime);
        }
    }

    /**
     * Restore volume from ducking
     */
    unduck(): void {
        if (!this.musicBusGain || !this.context) return;
        this.forceUnduck(this.context.currentTime);
    }

    private forceUnduck(startTime: number): void {
        if (!this.musicBusGain) return;

        const originalVolume = this.settings.volume;
        const fadeDuration = 0.5;

        // We assume we are restoring to settings.volume
        // Note: this overrides any scheduled ducks
        this.musicBusGain.gain.setValueAtTime(this.musicBusGain.gain.value, startTime);
        this.musicBusGain.gain.linearRampToValueAtTime(originalVolume, startTime + fadeDuration);
    }


    /**
     * Update settings
     *
     * @param newSettings - Partial settings to update
     */
    updateSettings(newSettings: Partial<MusicSettings>): void {
        this.settings = { ...this.settings, ...newSettings };

        if (newSettings.volume !== undefined) {
            this.setVolume(newSettings.volume);
        }

        // Handle other settings updates as needed
    }

    /**
     * Check if music is currently playing
     */
    isPlaying(): boolean {
        return this.layerManager?.isCurrentlyPlaying() ?? false;
    }

    /**
     * Register event listener
     *
     * @param event - Event type
     * @param listener - Callback function
     */
    on(event: MusicEngineEvent, listener: MusicEngineEventListener): void {
        if (!this.eventListeners.has(event)) {
            this.eventListeners.set(event, new Set());
        }
        this.eventListeners.get(event)!.add(listener);
    }

    /**
     * Unregister event listener
     *
     * @param event - Event type
     * @param listener - Callback function
     */
    off(event: MusicEngineEvent, listener: MusicEngineEventListener): void {
        const listeners = this.eventListeners.get(event);
        if (listeners) {
            listeners.delete(listener);
        }
    }

    /**
     * Emit event to all registered listeners
     *
     * @param event - Event type
     * @param data - Optional data to pass to listeners
     */
    private emit(event: MusicEngineEvent, data?: unknown): void {
        const listeners = this.eventListeners.get(event);
        if (listeners) {
            for (const listener of listeners) {
                try {
                    listener(data);
                } catch (error) {
                    console.error(`[MusicEngine] Error in ${event} listener:`, error);
                }
            }
        }
    }

    /**
     * Clean up resources
     */
    dispose(): void {
        this.stop();

        if (this.layerManager) {
            this.layerManager.dispose();
            this.layerManager = null;
        }

        if (this.context) {
            this.context.close().catch((error) => {
                console.warn('[MusicEngine] Error closing AudioContext:', error);
            });
            this.context = null;
        }

        this.eventListeners.clear();
        this.isInitialized = false;
    }
}
