/**
 * DOM Element References
 *
 * Centralized access to DOM elements used throughout the application.
 *
 * @module elements
 */

import type { SensorType } from './types/connections.js';

/**
 * Sensor element configuration
 */
export interface SensorElements {
    display: HTMLElement | null;
    connect: HTMLElement | null;
}

/**
 * All sensor elements by type
 */
export type SensorElementsMap = Record<SensorType, SensorElements>;

/**
 * Get DOM element by ID with null check
 *
 * @param id - Element ID
 * @returns The element or null if not found
 */
function getElement<T extends HTMLElement = HTMLElement>(id: string): T | null {
    const el = document.getElementById(id) as T | null;
    if (!el) {
        // console.warn(`Element with ID "${id}" not found`);
    }
    return el;
}

/**
 * Grouped elements by sensor type.
 * Uses getters to lazily retrieve elements from the DOM, ensuring they are found
 * even if this module is imported before the DOM is fully constructed.
 */
export const elements: SensorElementsMap = {
    get power() {
        return { display: getElement('power'), connect: getElement('connectPower') };
    },
    get heartrate() {
        return { display: getElement('heartrate'), connect: getElement('connectHeartrate') };
    },
    get cadence() {
        return { display: getElement('cadence'), connect: getElement('connectCadence') };
    },
    get gps() {
        return { display: null, connect: getElement('connectGps') };
    },
    get speed() {
        return { display: getElement('speed'), connect: getElement('connectSpeed') };
    },
    get distance() {
        return { display: getElement('distance'), connect: null };
    },
    get altitude() {
        return { display: getElement('altitude'), connect: null };
    },
    get treadmill() {
        return { display: getElement('value-incline'), connect: getElement('connectTreadmill') };
    },
    get treadmillSpeed() {
        return { display: getElement('treadmillSpeed'), connect: null };
    },
    get indoorBike() {
        return { display: null, connect: getElement('connectIndoorBike') };
    },
    get rower() {
        return { display: null, connect: getElement('connectRower') };
    },
    get crossTrainer() {
        return { display: null, connect: getElement('connectCrossTrainer') };
    },
    get stepClimber() {
        return { display: null, connect: getElement('connectStepClimber') };
    },
    get weightScale() {
        return { display: null, connect: getElement('connectWeightScale') };
    },
    get rsc() {
        return { display: null, connect: getElement('connectRsc') };
    },
    get concept2() {
        return { display: null, connect: getElement('connectConcept2') };
    },
    get skiErg() {
        return { display: null, connect: getElement('connectSkiErg') };
    },
};

/**
 * Get element by ID (type-safe helper)
 */
export { getElement };
