/**
 * Menu Interactions
 *
 * Provides enhanced keyboard navigation and click-outside-to-close
 * functionality for the main navigation menu.
 *
 * @module menuInteractions
 */

/**
 * Initialize menu keyboard navigation and click-outside behavior.
 *
 * Features:
 * - Arrow key navigation (Up/Down)
 * - Escape key to close menu
 * - Click outside to close menu
 * - Focus management
 */
export function initMenuInteractions(): void {
    const menu = document.getElementById('mainMenu') as HTMLDetailsElement | null;
    const summary = menu?.querySelector('summary');
    const controls = document.getElementById('controls');

    if (!menu || !summary || !controls) {
        return;
    }

    // Get all focusable menu items
    const getFocusableItems = (): HTMLElement[] => {
        return Array.from(
            controls.querySelectorAll<HTMLElement>(
                'button.menu-btn, .menu-toggle-container'
            )
        );
    };

    // Handle keyboard navigation within the menu
    const handleKeyDown = (event: KeyboardEvent): void => {
        if (!menu.open) return;

        const focusableItems = getFocusableItems();
        const currentIndex = focusableItems.findIndex(
            (item) => item === document.activeElement || item.contains(document.activeElement as Node)
        );

        switch (event.key) {
            case 'Escape':
                event.preventDefault();
                closeMenu();
                summary.focus();
                break;

            case 'ArrowDown':
                event.preventDefault();
                if (currentIndex < focusableItems.length - 1) {
                    focusableItems[currentIndex + 1].focus();
                } else {
                    // Wrap to first item
                    focusableItems[0]?.focus();
                }
                break;

            case 'ArrowUp':
                event.preventDefault();
                if (currentIndex > 0) {
                    focusableItems[currentIndex - 1].focus();
                } else {
                    // Wrap to last item
                    focusableItems[focusableItems.length - 1]?.focus();
                }
                break;

            case 'Home':
                event.preventDefault();
                focusableItems[0]?.focus();
                break;

            case 'End':
                event.preventDefault();
                focusableItems[focusableItems.length - 1]?.focus();
                break;

            case 'Tab':
                // Allow tab but trap within menu
                if (event.shiftKey && currentIndex === 0) {
                    event.preventDefault();
                    focusableItems[focusableItems.length - 1]?.focus();
                } else if (!event.shiftKey && currentIndex === focusableItems.length - 1) {
                    event.preventDefault();
                    focusableItems[0]?.focus();
                }
                break;
        }
    };

    // Close the menu
    const closeMenu = (): void => {
        menu.open = false;
        summary.setAttribute('aria-expanded', 'false');
    };

    // Open the menu
    const openMenu = (): void => {
        menu.open = true;
        summary.setAttribute('aria-expanded', 'true');
        // Focus first menu item after a short delay for animation
        requestAnimationFrame(() => {
            const focusableItems = getFocusableItems();
            focusableItems[0]?.focus();
        });
    };

    // Handle click outside to close
    const handleClickOutside = (event: MouseEvent): void => {
        if (!menu.open) return;

        const target = event.target as Node;
        if (!menu.contains(target)) {
            closeMenu();
        }
    };

    // Update aria-expanded when details toggles
    const handleToggle = (): void => {
        summary.setAttribute('aria-expanded', menu.open ? 'true' : 'false');

        if (menu.open) {
            // Add click outside listener
            // Use setTimeout to prevent immediate close from the click that opened it
            setTimeout(() => {
                document.addEventListener('click', handleClickOutside);
            }, 0);

            // Focus first item
            requestAnimationFrame(() => {
                const focusableItems = getFocusableItems();
                focusableItems[0]?.focus();
            });
        } else {
            document.removeEventListener('click', handleClickOutside);
        }
    };

    // Handle keyboard on summary element
    const handleSummaryKeyDown = (event: KeyboardEvent): void => {
        if (event.key === 'Enter' || event.key === ' ') {
            // Let the default behavior handle opening
            return;
        }

        if (event.key === 'ArrowDown' && !menu.open) {
            event.preventDefault();
            openMenu();
        }
    };

    // Close menu when a menu item is clicked
    const handleMenuItemClick = (event: Event): void => {
        const target = event.target as HTMLElement;

        // Don't close for toggle switches (let them toggle)
        if (target.closest('.menu-toggle-container')) {
            return;
        }

        // Close menu for regular buttons
        if (target.closest('.menu-btn')) {
            // Small delay to allow the button's action to process
            setTimeout(() => {
                closeMenu();
            }, 50);
        }
    };

    // Attach event listeners
    menu.addEventListener('toggle', handleToggle);
    menu.addEventListener('keydown', handleKeyDown);
    summary.addEventListener('keydown', handleSummaryKeyDown);
    controls.addEventListener('click', handleMenuItemClick);

    // Handle toggle container keyboard activation
    const toggleContainers = controls.querySelectorAll<HTMLElement>('.menu-toggle-container');
    toggleContainers.forEach((container) => {
        container.addEventListener('keydown', (event: KeyboardEvent) => {
            if (event.key === 'Enter' || event.key === ' ') {
                event.preventDefault();
                const checkbox = container.querySelector('input[type="checkbox"]') as HTMLInputElement | null;
                if (checkbox) {
                    checkbox.checked = !checkbox.checked;
                    checkbox.dispatchEvent(new Event('change', { bubbles: true }));

                    // Update aria-checked
                    container.setAttribute('aria-checked', checkbox.checked ? 'true' : 'false');
                }
            }
        });

        // Sync aria-checked with checkbox state
        const checkbox = container.querySelector('input[type="checkbox"]') as HTMLInputElement | null;
        if (checkbox) {
            checkbox.addEventListener('change', () => {
                container.setAttribute('aria-checked', checkbox.checked ? 'true' : 'false');
            });
        }
    });

    // Initial aria-expanded state
    summary.setAttribute('aria-expanded', 'false');
}

/**
 * Initialize all menu-related functionality.
 * Call this from the main initialization.
 */
export function initMenu(): void {
    initMenuInteractions();
}
