/**
 * Navigation Bar Component
 *
 * Handles the bottom navigation bar with feature flag support.
 */

import { IRouter } from '../router/types.js';
import { ViewId } from '../router/view-ids.js';
import { featureFlagService } from '../services/FeatureFlagService.js';

interface NavItem {
    viewId: string;
    icon: string;
    label: string;
    featureFlag?: string;
}

const NAV_ITEMS: NavItem[] = [
    { viewId: ViewId.Dashboard, icon: '', label: 'Dashboard' },
    { viewId: ViewId.History, icon: '', label: 'History' },
    { viewId: ViewId.Workouts, icon: '', label: 'Workouts', featureFlag: 'workouts' },
    { viewId: ViewId.Fitness, icon: '', label: 'Fitness', featureFlag: 'fitness' },
    { viewId: ViewId.Plans, icon: '', label: 'Plans', featureFlag: 'plans' },
    { viewId: ViewId.Settings, icon: '', label: 'Settings' },
    { viewId: ViewId.Debug, icon: '', label: 'Debug', featureFlag: 'debugView' },
];

export class NavBar {
    private container: HTMLElement;
    private router: IRouter;

    constructor(router: IRouter) {
        this.router = router;
        this.container = document.createElement('nav');
        this.container.id = 'bottom-nav';
        this.container.className = 'bottom-nav';

        this.render();
        document.body.appendChild(this.container);

        // Listen for route changes
        window.addEventListener('route-changed', (e: Event) => {
            const detail = (e as CustomEvent).detail;
            this.setActive(detail.viewId);
        });
    }

    /**
     * Get nav items filtered by enabled feature flags
     */
    private getEnabledNavItems(): NavItem[] {
        return NAV_ITEMS.filter((item) => {
            if (!item.featureFlag) {
                return true;
            }
            return featureFlagService.isEnabled(item.featureFlag);
        });
    }

    private render(): void {
        const enabledItems = this.getEnabledNavItems();

        this.container.innerHTML = enabledItems
            .map(
                (item, index) => `
            <button class="nav-item${index === 0 ? ' active' : ''}" data-target="${item.viewId}" data-testid="nav-${item.viewId}">
                <span class="nav-icon">${item.icon}</span>
                <span class="nav-label">${item.label}</span>
            </button>
        `
            )
            .join('');

        this.container.querySelectorAll('.nav-item').forEach((btn) => {
            btn.addEventListener('click', () => {
                const target = (btn as HTMLElement).dataset.target;
                if (target === ViewId.Dashboard) {
                    this.router.navigate('/');
                } else if (target) {
                    this.router.navigate('/' + target);
                }
            });
        });
    }

    public setActive(viewId: string): void {
        this.container.querySelectorAll('.nav-item').forEach((btn) => {
            const target = (btn as HTMLElement).dataset.target;
            if (target === viewId) {
                btn.classList.add('active');
                btn.setAttribute('aria-current', 'page');
            } else {
                btn.classList.remove('active');
                btn.removeAttribute('aria-current');
            }
        });
    }
}
