import { stateEventBus } from '../state/EventBus.js';
import { MeanMaximalPowerCalculator } from '../calculations/MeanMaximalPower.js';
import { personalRecordsStore } from '../state/PersonalRecordsStore.js';
import { showNotification } from '../ui/notifications.js';
import { settingsStore } from '../state/SettingsStore.js';
import { showConfirmation } from '../ui/modal.js';

export class PersonalRecordsManager {
    private calculator = new MeanMaximalPowerCalculator();
    private isListening = false;

    constructor() {
        this.start();
    }

    public getCurrentSessionMax(): Record<number, number> {
        return this.calculator.getCurrentMax();
    }

    public start() {
        if (this.isListening) return;
        this.isListening = true;

        // Reset calculator on workout start
        stateEventBus.on('workout:start', () => {
            this.calculator.reset();
        });

        // Listen for power updates
        stateEventBus.on('power:update', async (data) => {
            const updates = this.calculator.addSample(data.value);

            if (updates) {
                // Check against persistent storage
                // We do this async to not block the main loop logic
                for (const [durationStr, value] of Object.entries(updates)) {
                    const duration = Number(durationStr);
                    const { isAllTime, isSeason } = await personalRecordsStore.checkAndAddRecord(
                        duration,
                        value,
                        data.timestamp
                    );

                    if (isAllTime) {
                        this.notifyNewRecord(duration, value, 'All-Time');

                        // Check for FTP improvemnent (20 min = 1200 sec)
                        if (duration === 1200) {
                            this.checkFtpImprovement(value);
                        }
                    } else if (isSeason) {
                        this.notifyNewRecord(duration, value, 'Season');
                    }
                }
            }
        });
    }

    private async checkFtpImprovement(avgPower20Min: number) {
        const estFtp = Math.round(avgPower20Min * 0.95);
        const currentFtp = settingsStore.getAll().ftp;

        // Only suggest if improvement is significant (e.g. > 2%)
        if (estFtp > currentFtp * 1.02) {
            const confirmed = await showConfirmation(
                'New FTP Detected',
                `Your recent 20-minute effort of ${Math.round(avgPower20Min)}W suggests a new FTP of ${estFtp}W. (Current: ${currentFtp}W)<br><br>Would you like to update your FTP?`,
                {
                    confirmText: 'Update FTP',
                    confirmVariant: 'primary',
                    icon: '',
                }
            );

            if (confirmed) {
                await settingsStore.updateFtp(estFtp, 'estimated');
                showNotification(`FTP updated to ${estFtp}W`, 'success');
            }
        }
    }

    private notifyNewRecord(duration: number, value: number, type: string) {
        // Format duration
        let durationLabel = `${duration}s`;
        if (duration >= 60) durationLabel = `${Math.floor(duration / 60)}m`;

        const message = `New ${type} Record! ${durationLabel} @ ${Math.round(value)}W`;

        showNotification(message, 'success', 5000);

        // Emit event for other UI components (e.g. confetti, special sounds)
        // We might want to expand EventMap first, but for now this is enough.
    }
}

// Singleton instance
export const personalRecordsManager = new PersonalRecordsManager();
