import { pauseWorkout, resumeWorkout } from '../ui/time.js';
import type { TimeState } from '../getInitState.js';

export class MediaSessionManager {
    private timeState: TimeState | null = null;
    private hasInitialized = false;

    /**
     * Initialize Media Session API integration
     */
    public init(timeState: TimeState): void {
        if (typeof navigator === 'undefined' || !('mediaSession' in navigator)) {
            console.log('Media Session API not supported');
            return;
        }

        if (this.hasInitialized) return;
        this.hasInitialized = true;
        this.timeState = timeState;

        this.setupMetadata();
        this.setupActionHandlers();
        this.setupEventListeners();

        console.log('Media Session Manager initialized');
    }

    private setupMetadata(): void {
        navigator.mediaSession.metadata = new MediaMetadata({
            title: 'No Workout Active',
            artist: 'Bike Power Tracker',
            album: 'Ready to Start',
            artwork: [
                { src: 'assets/icons/icon-96.png', sizes: '96x96', type: 'image/png' },
                { src: 'assets/icons/icon-128.png', sizes: '128x128', type: 'image/png' },
                { src: 'assets/icons/icon-180.png', sizes: '180x180', type: 'image/png' },
                { src: 'assets/icons/icon-192.png', sizes: '192x192', type: 'image/png' },
                { src: 'assets/icons/icon-512.png', sizes: '512x512', type: 'image/png' },
            ],
        });
    }

    private updateMetadata(status: string): void {
        if (!navigator.mediaSession.metadata) return;

        navigator.mediaSession.metadata.title = status;
        navigator.mediaSession.metadata.album = 'Bike Power Tracker';
    }

    private setupActionHandlers(): void {
        try {
            // Play -> Resume
            navigator.mediaSession.setActionHandler('play', () => {
                if (this.timeState && !this.timeState.running && this.timeState.startTime) {
                    resumeWorkout(this.timeState);
                }
            });

            // Pause -> Pause
            navigator.mediaSession.setActionHandler('pause', () => {
                if (this.timeState && this.timeState.running) {
                    pauseWorkout(this.timeState);
                }
            });

            // Stop -> Pause (safer)
            navigator.mediaSession.setActionHandler('stop', () => {
                if (this.timeState && this.timeState.running) {
                    pauseWorkout(this.timeState);
                }
            });
        } catch (e) {
            console.warn('Error setting up media session handlers', e);
        }
    }

    private setupEventListeners(): void {
        document.addEventListener('workoutStarted', () => {
            if (navigator.mediaSession) {
                navigator.mediaSession.playbackState = 'playing';
                this.updateMetadata('Workout Active');
            }
        });

        document.addEventListener('workoutPaused', () => {
            if (navigator.mediaSession) {
                navigator.mediaSession.playbackState = 'paused';
                this.updateMetadata('Workout Paused');
            }
        });

        document.addEventListener('workoutResumed', () => {
            if (navigator.mediaSession) {
                navigator.mediaSession.playbackState = 'playing';
                this.updateMetadata('Workout Active');
            }
        });

        document.addEventListener('workoutComplete', () => {
            if (navigator.mediaSession) {
                navigator.mediaSession.playbackState = 'none';
                this.updateMetadata('Workout Finished');
            }
        });
    }
}

export const mediaSessionManager = new MediaSessionManager();
