import { getSettings } from '../config/settings.js';
import { settingsStore } from '../state/SettingsStore.js';
import { getSportSettings } from '../config/sport.js';
import { audioManager } from '../ui/audio.js';
import { musicController } from '../audio/MusicController.js';
import { workerTimer } from './WorkerTimer.js';
import type { MeasurementsState } from '../measurements-state.js';
import type { TimeState } from '../getInitState.js';
import type { ActiveWorkoutState } from '../workouts/types.js';

/**
 * Current workout metrics for announcements
 */
interface WorkoutMetrics {
    power: number | null;
    heartrate: number | null;
    cadence: number | null;
    speedKmh: number | null;
    pace: string | null;
    distanceKm: number | null;
    elapsedMs: number;
}

/**
 * Calculate total distance from GPS points in km
 */
function calculateTotalDistanceKm(gpsPoints: { lat: number; lon: number }[]): number {
    if (gpsPoints.length < 2) return 0;

    let totalMeters = 0;
    const R = 6371000; // Earth radius in meters

    for (let i = 1; i < gpsPoints.length; i++) {
        const prev = gpsPoints[i - 1];
        const curr = gpsPoints[i];

        // Haversine formula
        const lat1 = (prev.lat * Math.PI) / 180;
        const lat2 = (curr.lat * Math.PI) / 180;
        const deltaLat = ((curr.lat - prev.lat) * Math.PI) / 180;
        const deltaLon = ((curr.lon - prev.lon) * Math.PI) / 180;

        const a =
            Math.sin(deltaLat / 2) * Math.sin(deltaLat / 2) +
            Math.cos(lat1) * Math.cos(lat2) * Math.sin(deltaLon / 2) * Math.sin(deltaLon / 2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

        totalMeters += R * c;
    }

    return totalMeters / 1000;
}

export class VoiceFeedback {
    private synth: SpeechSynthesis | null;
    private voice: SpeechSynthesisVoice | null = null;
    private measurementsState: MeasurementsState | null = null;
    private timeState: TimeState | null = null;
    private lastTimeIntervalMs: number = 0;
    private lastDistanceIntervalKm: number = 0;
    private workoutState: ActiveWorkoutState | null = null;
    private lastTargetAlertTime: number = 0;
    private checkInterval: number | null = null;
    private isActive: boolean = false;

    constructor() {
        if (typeof window !== 'undefined') {
            this.synth = window.speechSynthesis || null;
        } else {
            this.synth = null;
        }

        if (!this.synth) {
            console.warn('Speech Synthesis not supported on this device');
            return;
        }

        // Wait for voices to be loaded
        if (this.synth.onvoiceschanged !== undefined) {
            this.synth.onvoiceschanged = () => this.loadVoice();
        }
        this.loadVoice();
    }

    /**
     * Initialize with state references for enhanced announcements
     */
    init(measurementsState: MeasurementsState, timeState: TimeState): void {
        this.measurementsState = measurementsState;
        this.timeState = timeState;
    }

    /**
     * Update active workout state
     */
    updateWorkoutState(state: ActiveWorkoutState | null): void {
        this.workoutState = state;
    }

    /**
     * Start interval-based announcements
     */
    startIntervalAnnouncements(): void {
        if (this.isActive || !this.measurementsState || !this.timeState) {
            return;
        }

        const settings = getSettings();
        if (
            settings.enhancedVoice.timeIntervalMinutes === 0 &&
            settings.enhancedVoice.distanceIntervalKm === 0 &&
            !settings.enhancedVoice.workoutTargetFeedback
        ) {
            return;
        }

        this.isActive = true;
        this.lastTimeIntervalMs = 0;
        this.lastDistanceIntervalKm = 0;

        // Check every second using WorkerTimer for background support
        this.checkInterval = workerTimer.setInterval(() => this.checkIntervalAnnouncements(), 1000);

        // Keep audio context alive for background playback
        audioManager.startBackgroundKeepAlive();

        console.log('Enhanced voice announcements started (Worker backed)');
    }

    /**
     * Stop interval-based announcements
     */
    stopIntervalAnnouncements(): void {
        if (this.checkInterval !== null) {
            workerTimer.clearInterval(this.checkInterval);
            this.checkInterval = null;
        }

        // Stop keep-alive
        audioManager.stopBackgroundKeepAlive();

        this.isActive = false;
        this.lastTimeIntervalMs = 0;
        this.lastDistanceIntervalKm = 0;
    }

    /**
     * Reset state
     */
    reset(): void {
        this.stopIntervalAnnouncements();
    }

    private loadVoice(): void {
        if (!this.synth) return;

        const voices = this.synth.getVoices();
        const settings = getSettings();

        if (settings.enhancedVoice.voiceURI) {
            this.voice = voices.find((v) => v.voiceURI === settings.enhancedVoice.voiceURI) || null;
        }

        if (!this.voice) {
            // Prefer English voices
            this.voice = voices.find((v) => v.lang.startsWith('en')) || voices[0] || null;
        }
    }

    /**
     * Get all available voices
     */
    public getAvailableVoices(): SpeechSynthesisVoice[] {
        if (!this.synth) return [];
        return this.synth.getVoices().sort((a, b) => a.name.localeCompare(b.name));
    }

    /**
     * Reload voice from settings
     */
    public reloadVoice(): void {
        this.loadVoice();
    }

    private formatTimeForSpeech(ms: number): string {
        const totalSeconds = Math.floor(ms / 1000);
        const hours = Math.floor(totalSeconds / 3600);
        const minutes = Math.floor((totalSeconds % 3600) / 60);
        const seconds = totalSeconds % 60;

        const parts = [];
        if (hours > 0) parts.push(`${hours} hour${hours !== 1 ? 's' : ''}`);
        if (minutes > 0) parts.push(`${minutes} minute${minutes !== 1 ? 's' : ''}`);
        if (seconds > 0 || parts.length === 0) parts.push(`${seconds} second${seconds !== 1 ? 's' : ''}`);

        return parts.join(' ');
    }

    private formatTimeShort(ms: number): string {
        const totalSeconds = Math.floor(ms / 1000);
        const hours = Math.floor(totalSeconds / 3600);
        const minutes = Math.floor((totalSeconds % 3600) / 60);

        if (hours > 0) {
            return `${hours} hour${hours !== 1 ? 's' : ''} ${minutes} minute${minutes !== 1 ? 's' : ''}`;
        }
        return `${minutes} minute${minutes !== 1 ? 's' : ''}`;
    }

    public speak(text: string): void {
        if (!this.synth) return;

        const settings = getSettings();
        if (!settings.voiceEnabled) return;

        if (this.synth.speaking) {
            this.synth.cancel();
            // If we cancel, ensure we don't leave volume down if the previous onend didn't fire
            // audioManager.unduck(); // Actually, better to just duck again for the new one
        }

        const utterance = new SpeechSynthesisUtterance(text);
        if (this.voice) {
            utterance.voice = this.voice;
        }

        // Apply speech rate
        utterance.rate = settings.enhancedVoice.speechRate;

        // Duck music volume
        utterance.onstart = () => {
            audioManager.duck();
            musicController.duck();
        };

        const restoreVolume = () => {
            // Only restore if this was the last utterance?
            // SpeechSynthesis is a queue, but here we cancel previous.
            // If we cancel, onend runs?
            audioManager.unduck();
            musicController.unduck();
        };

        utterance.onend = restoreVolume;
        utterance.onerror = (e) => {
            console.warn('Speech synthesis error:', e);
            restoreVolume();
        };

        this.synth.speak(utterance);
    }

    public announceLap(lapNumber: number, timeMs: number, avgPower: number, avgSpeed: number = 0): void {
        if (!this.synth) return;

        const settings = getSettings();
        if (!settings.voiceLaps) return;

        const sportSettings = getSportSettings();
        const isRunning = sportSettings.activeSport === 'running' || sportSettings.activeSport === 'walking';

        const timeStr = this.formatTimeForSpeech(timeMs);
        let text = `Lap ${lapNumber}. Time ${timeStr}.`;

        if (isRunning && avgSpeed > 0) {
            // Calculate pace in min/km
            const paceSec = 1000 / avgSpeed;
            const min = Math.floor(paceSec / 60);
            const sec = Math.round(paceSec % 60);
            text += ` Average Pace ${min} ${sec} per kilometer.`;
        } else {
            text += ` Average Power ${Math.round(avgPower)} watts.`;
        }

        this.speak(text);
    }

    public announceZoneChange(zoneName: string): void {
        if (!this.synth) return;

        const settings = getSettings();
        if (!settings.voiceZones) return;

        // Smart announcements: shorter message
        if (settings.enhancedVoice.smartAnnouncements) {
            this.speak(zoneName);
            return;
        }

        const text = `Entering ${zoneName} Zone`;
        this.speak(text);
    }

    public announceCountdown(count: number): void {
        if (!this.synth) return;

        const settings = getSettings();
        if (!settings.countdown.enableVoice) return;

        const text = count === 0 ? 'Go!' : `${count}`;
        this.speak(text);
    }

    /**
     * Check if interval-based announcements should be triggered
     */
    private checkIntervalAnnouncements(): void {
        const isRecording = this.timeState?.running ?? false;
        const isWorkoutRunning = (this.workoutState?.isRunning && !this.workoutState?.isPaused) ?? false;

        if (!this.measurementsState || (!isRecording && !isWorkoutRunning)) {
            return;
        }

        const settings = getSettings();

        // Check time-based announcements (only if recording)
        if (isRecording && settings.enhancedVoice.timeIntervalMinutes > 0) {
            this.checkTimeAnnouncement(settings.enhancedVoice.timeIntervalMinutes);
        }

        // Check distance-based announcements (only if recording or we want distance feedback during workout?)
        // Let's assume distance accumulator works regardless, but keeping it simple: tied to recording usually?
        // Actually calculateTotalDistanceKm depends on GPS points which are accumulated in MeasurementsState
        // MeasurementsState.gps is array. Does it accumulate if not recording?
        // Usually MeasurementsState adds points always? No, MergeMeasurements likely only adds if recording?
        // Let's stick to restriction: generic intervals only if recording.
        if (isRecording && settings.enhancedVoice.distanceIntervalKm > 0) {
            this.checkDistanceAnnouncement(settings.enhancedVoice.distanceIntervalKm);
        }

        // Check target deviation (if workout is running)
        if (isWorkoutRunning && settings.enhancedVoice.workoutTargetFeedback && this.workoutState) {
            this.checkTargetDeviation();
        }
    }

    /**
     * Check for time-based announcements
     */
    private checkTimeAnnouncement(intervalMinutes: number): void {
        if (!this.timeState || !this.timeState.startTime) return;

        const elapsedMs = Date.now() - this.timeState.startTime;
        const intervalMs = intervalMinutes * 60 * 1000;

        const currentInterval = Math.floor(elapsedMs / intervalMs);
        const lastInterval = Math.floor(this.lastTimeIntervalMs / intervalMs);

        if (currentInterval > lastInterval && elapsedMs > 0) {
            this.announceCurrentMetrics('time');
            this.lastTimeIntervalMs = elapsedMs;
        }
    }

    /**
     * Check for distance-based announcements
     */
    private checkDistanceAnnouncement(intervalKm: number): void {
        if (!this.measurementsState) return;

        const totalKm = calculateTotalDistanceKm(this.measurementsState.gps);

        const currentInterval = Math.floor(totalKm / intervalKm);
        const lastInterval = Math.floor(this.lastDistanceIntervalKm / intervalKm);

        if (currentInterval > lastInterval && totalKm > 0) {
            this.announceCurrentMetrics('distance');
            this.lastDistanceIntervalKm = totalKm;
        }
    }

    private checkTargetDeviation(): void {
        if (!this.workoutState || !this.measurementsState) return;

        const settings = getSettings();
        const now = Date.now();
        // Debounce: 10 seconds
        if (now - this.lastTargetAlertTime < 10000) return;

        const target = this.workoutState.currentAbsoluteTarget;
        if (!target) return;

        // Only handle power for now
        if (target.type !== 'power') return;

        // Get latest power value
        const powerData = this.measurementsState.power;
        const currentPower = powerData.length > 0 ? powerData[powerData.length - 1].value : 0;

        const targetMin = target.min;
        const targetMax = target.max;

        // Ignore if stopped or very low power (coasting/stopped)
        if (currentPower < 10) return;

        let msg = '';
        // Tolerance buffer (10 Watts) to prevent jitter
        const TOLERANCE = 10;

        if (currentPower < targetMin - TOLERANCE) {
            msg = settings.enhancedVoice.smartAnnouncements ? 'Power Low' : 'Power too low';
        } else if (currentPower > targetMax + TOLERANCE) {
            msg = settings.enhancedVoice.smartAnnouncements ? 'Power High' : 'Power too high';
        }

        if (msg) {
            this.speak(msg);
            this.lastTargetAlertTime = now;
        }
    }

    /**
     * Get current workout metrics
     */
    private getCurrentMetrics(): WorkoutMetrics {
        const metrics: WorkoutMetrics = {
            power: null,
            heartrate: null,
            cadence: null,
            speedKmh: null,
            pace: null,
            distanceKm: null,
            elapsedMs: 0,
        };

        if (!this.measurementsState || !this.timeState) return metrics;

        // Get latest values (within last 5 seconds)
        const now = Date.now();
        const recentThreshold = now - 5000;

        // Power
        const recentPower = this.measurementsState.power.filter((p) => p.timestamp > recentThreshold);
        if (recentPower.length > 0) {
            metrics.power = Math.round(recentPower[recentPower.length - 1].value);
        }

        // Heart rate
        const recentHr = this.measurementsState.heartrate.filter((h) => h.timestamp > recentThreshold);
        if (recentHr.length > 0) {
            metrics.heartrate = Math.round(recentHr[recentHr.length - 1].value);
        }

        // Cadence
        const recentCadence = this.measurementsState.cadence.filter((c) => c.timestamp > recentThreshold);
        if (recentCadence.length > 0) {
            metrics.cadence = Math.round(recentCadence[recentCadence.length - 1].value);
        }

        // Speed
        const recentSpeed = this.measurementsState.speed.filter((s) => s.timestamp > recentThreshold);
        if (recentSpeed.length > 0) {
            metrics.speedKmh = Math.round(recentSpeed[recentSpeed.length - 1].value * 10) / 10;
        }

        // Calculate pace from speed (min/km)
        if (metrics.speedKmh && metrics.speedKmh > 0.5) {
            const minPerKm = 60 / metrics.speedKmh;
            const minutes = Math.floor(minPerKm);
            const seconds = Math.round((minPerKm - minutes) * 60);
            metrics.pace = `${minutes} minute${minutes !== 1 ? 's' : ''} ${seconds} per kilometer`;
        }

        // Distance
        metrics.distanceKm = Math.round(calculateTotalDistanceKm(this.measurementsState.gps) * 100) / 100;

        // Elapsed time
        if (this.timeState.startTime) {
            metrics.elapsedMs = now - this.timeState.startTime;
        }

        return metrics;
    }

    private processTemplate(template: string, metrics: WorkoutMetrics): string {
        let text = template;
        const val = (v: number | null): string => (v !== null ? v.toString() : 'unavailable');

        text = text.replace(/{power}/gi, val(metrics.power));
        text = text.replace(/{heartrate}|{hr}/gi, val(metrics.heartrate));
        text = text.replace(/{cadence}|{rpm}/gi, val(metrics.cadence));
        text = text.replace(/{speed}/gi, metrics.speedKmh !== null ? metrics.speedKmh.toFixed(1) : 'unavailable');
        text = text.replace(/{pace}/gi, metrics.pace || 'unavailable');
        text = text.replace(
            /{distance}|{dist}/gi,
            metrics.distanceKm !== null ? metrics.distanceKm.toFixed(1) : 'unavailable'
        );
        text = text.replace(/{time}/gi, this.formatTimeShort(metrics.elapsedMs));

        return text;
    }

    /**
     * Announce current metrics based on settings
     */
    private announceCurrentMetrics(trigger: 'time' | 'distance'): void {
        const settings = getSettings();
        const metrics = this.getCurrentMetrics();

        // Check for custom template
        if (
            settings.enhancedVoice.announcementTemplate &&
            settings.enhancedVoice.announcementTemplate.trim().length > 0
        ) {
            const text = this.processTemplate(settings.enhancedVoice.announcementTemplate, metrics);
            this.speak(text);
            console.log(`Voice announcement (custom): ${text}`);
            return;
        }

        const metricsSettings = settings.enhancedVoice.metrics;
        const { smartAnnouncements } = settings.enhancedVoice;
        let isHighIntensity = false;

        if (smartAnnouncements) {
            const userSettings = settingsStore.getAll();
            const powerThreshold = (userSettings.ftp || 200) * 0.85; // Upper Zone 3 / Zone 4
            const hrThreshold = (userSettings.maxHeartrate || 190) * 0.85; // Upper Zone 3 / Zone 4

            if (metrics.power !== null && metrics.power >= powerThreshold) isHighIntensity = true;
            if (metrics.heartrate !== null && metrics.heartrate >= hrThreshold) isHighIntensity = true;
        }

        const parts: string[] = [];

        // Helper for smart formatting
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        const addMetric = (condition: boolean, normal: string, smart: string) => {
            if (condition) {
                parts.push(isHighIntensity ? smart : normal);
            }
        };

        // Start with the trigger announcement
        if (trigger === 'time' && metricsSettings.time) {
            parts.push(this.formatTimeShort(metrics.elapsedMs));
        } else if (trigger === 'distance' && metricsSettings.distance && metrics.distanceKm !== null) {
            parts.push(
                isHighIntensity ? `${metrics.distanceKm.toFixed(1)}k` : `${metrics.distanceKm.toFixed(1)} kilometers`
            );
        }

        // Add other metrics based on settings
        if (metricsSettings.power && metrics.power !== null) {
            addMetric(true, `power ${metrics.power} watts`, `${metrics.power} watts`);
        }

        if (metricsSettings.heartrate && metrics.heartrate !== null) {
            addMetric(true, `heart rate ${metrics.heartrate}`, `${metrics.heartrate}`);
        }

        if (metricsSettings.speed && metrics.speedKmh !== null) {
            if (isHighIntensity) {
                parts.push(`${metrics.speedKmh.toFixed(1)} kph`);
            } else {
                parts.push(`speed ${metrics.speedKmh.toFixed(1)} k p h`);
            }
        }

        if (metricsSettings.pace && metrics.pace !== null) {
            // Simplify pace in high intensity if possible, but the string is pre-formatted.
            // Keeping as is for now, maybe stripping "per kilometer" if smart?
            if (isHighIntensity) {
                parts.push(metrics.pace.replace(' per kilometer', ''));
            } else {
                parts.push(`pace ${metrics.pace}`);
            }
        }

        if (metricsSettings.cadence && metrics.cadence !== null) {
            addMetric(true, `cadence ${metrics.cadence}`, `${metrics.cadence} rpm`);
        }

        // Include distance if not already the trigger
        if (trigger === 'time' && metricsSettings.distance && metrics.distanceKm !== null && metrics.distanceKm > 0) {
            parts.push(
                isHighIntensity
                    ? `${metrics.distanceKm.toFixed(1)}k`
                    : `distance ${metrics.distanceKm.toFixed(1)} kilometers`
            );
        }

        // Include time if not already the trigger
        if (trigger === 'distance' && metricsSettings.time) {
            if (isHighIntensity) {
                parts.push(this.formatTimeShort(metrics.elapsedMs));
            } else {
                parts.push(`time ${this.formatTimeShort(metrics.elapsedMs)}`);
            }
        }

        if (parts.length > 0) {
            const text = parts.join('. ') + '.';
            this.speak(text);
            console.log(`Voice announcement (${trigger}): ${text}`);
        }
    }
}

export const voiceFeedback = new VoiceFeedback();
