/**
 * Modal Component
 *
 * Reusable modal dialog system for confirmations and content display.
 * Fully accessible with keyboard navigation and screen reader support.
 *
 * @module modal
 */

import { announce } from './accessibility.js';
import { downloadWorkoutImage } from './shareWithImage.js';
import type { ZoneDistribution } from '../zone-state.js';
import { POWER_ZONE_COLORS } from '../styles/zone-colors.js';

/**
 * Modal button configuration
 */
export interface ModalButton {
    text: string;
    variant: 'primary' | 'secondary' | 'danger';
    onClick: () => void;
}

/**
 * Modal configuration options
 */
export interface ModalOptions {
    title: string;
    content: string | HTMLElement;
    buttons: ModalButton[];
    icon?: string;
    onClose?: () => void;
    closeOnOverlay?: boolean;
}

/**
 * Create a modal element
 */
function createModalElement(options: ModalOptions): HTMLElement {
    const modal = document.createElement('div');
    modal.className = 'custom-modal';
    modal.setAttribute('role', 'dialog');
    modal.setAttribute('aria-modal', 'true');
    modal.setAttribute('aria-labelledby', 'modal-title');

    const titleText = options.icon ? `${options.icon} ${options.title}` : options.title;

    modal.innerHTML = `
        <div class="custom-modal-overlay"></div>
        <div class="custom-modal-container">
            <div class="custom-modal-header">
                <h2 id="modal-title">${titleText}</h2>
                <button class="modal-close-btn" aria-label="Close dialog">&times;</button>
            </div>
            <div class="custom-modal-body"></div>
            <div class="custom-modal-footer"></div>
        </div>
    `;

    // Add content
    const body = modal.querySelector('.custom-modal-body') as HTMLElement;
    if (typeof options.content === 'string') {
        body.innerHTML = options.content;
    } else {
        body.appendChild(options.content);
    }

    // Add buttons
    const footer = modal.querySelector('.custom-modal-footer') as HTMLElement;
    options.buttons.forEach((btn, index) => {
        const button = document.createElement('button');
        button.textContent = btn.text;
        button.className = `modal-btn modal-btn-${btn.variant}`;
        button.addEventListener('click', btn.onClick);

        // Auto-focus the first primary button, or first button if no primary
        if (btn.variant === 'primary' || (index === 0 && !options.buttons.some((b) => b.variant === 'primary'))) {
            button.setAttribute('data-autofocus', 'true');
        }

        footer.appendChild(button);
    });

    return modal;
}

/**
 * Focus trap for modal accessibility
 */
function trapFocus(modal: HTMLElement): () => void {
    const focusableElements = modal.querySelectorAll<HTMLElement>(
        'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
    );
    const firstFocusable = focusableElements[0];
    const lastFocusable = focusableElements[focusableElements.length - 1];

    const handleTabKey = (e: KeyboardEvent) => {
        if (e.key !== 'Tab') return;

        if (e.shiftKey) {
            if (document.activeElement === firstFocusable) {
                e.preventDefault();
                lastFocusable?.focus();
            }
        } else {
            if (document.activeElement === lastFocusable) {
                e.preventDefault();
                firstFocusable?.focus();
            }
        }
    };

    modal.addEventListener('keydown', handleTabKey);
    return () => modal.removeEventListener('keydown', handleTabKey);
}

/**
 * Show a modal dialog
 *
 * @param options - Modal configuration
 * @returns A function to close the modal
 */
export function showModal(options: ModalOptions): () => void {
    const modal = createModalElement(options);
    const previousActiveElement = document.activeElement as HTMLElement;

    document.body.appendChild(modal);

    // Force reflow for animation
    modal.offsetHeight;
    modal.classList.add('modal-visible');

    // Set up focus trap
    const removeFocusTrap = trapFocus(modal);

    // Focus the autofocus element or first focusable
    const autofocusEl = modal.querySelector<HTMLElement>('[data-autofocus]');
    setTimeout(() => {
        autofocusEl?.focus();
    }, 50);

    // Close function
    const closeModal = () => {
        modal.classList.remove('modal-visible');

        // Remove after animation
        setTimeout(() => {
            modal.remove();
            removeFocusTrap();

            // Restore focus
            previousActiveElement?.focus();

            options.onClose?.();
        }, 200);
    };

    // Close button
    const closeBtn = modal.querySelector('.modal-close-btn');
    closeBtn?.addEventListener('click', closeModal);

    // Overlay click
    if (options.closeOnOverlay !== false) {
        const overlay = modal.querySelector('.custom-modal-overlay');
        overlay?.addEventListener('click', closeModal);
    }

    // Escape key
    const handleEscape = (e: KeyboardEvent) => {
        if (e.key === 'Escape') {
            e.preventDefault();
            closeModal();
        }
    };
    document.addEventListener('keydown', handleEscape);

    // Cleanup escape listener when modal closes
    const originalOnClose = options.onClose;
    options.onClose = () => {
        document.removeEventListener('keydown', handleEscape);
        originalOnClose?.();
    };

    announce(`${options.title} dialog opened`, 'polite');

    return closeModal;
}

/**
 * Show a confirmation dialog
 *
 * @param title - Dialog title
 * @param message - Confirmation message
 * @param options - Additional options
 * @returns Promise resolving to true if confirmed, false if cancelled
 */
export function showConfirmation(
    title: string,
    message: string,
    options: {
        confirmText?: string;
        cancelText?: string;
        confirmVariant?: 'primary' | 'danger';
        icon?: string;
    } = {}
): Promise<boolean> {
    return new Promise((resolve) => {
        const { confirmText = 'Confirm', cancelText = 'Cancel', confirmVariant = 'primary', icon } = options;

        let closeModal: (() => void) | null = null;

        closeModal = showModal({
            title,
            content: `<p class="modal-message">${message}</p>`,
            icon,
            buttons: [
                {
                    text: cancelText,
                    variant: 'secondary',
                    onClick: () => {
                        closeModal?.();
                        resolve(false);
                    },
                },
                {
                    text: confirmText,
                    variant: confirmVariant,
                    onClick: () => {
                        closeModal?.();
                        resolve(true);
                    },
                },
            ],
            onClose: () => resolve(false),
        });
    });
}

import { type WorkoutSummary, calculateWorkoutSummary } from '../calculations/summary.js';
import { type LapSummary } from '../calculations/lap-calculator.js';
export { type WorkoutSummary, calculateWorkoutSummary };

/**
 * Format duration in milliseconds to human readable string
 */
export function formatDurationLong(ms: number): string {
    const totalSeconds = Math.floor(ms / 1000);
    const hours = Math.floor(totalSeconds / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const seconds = totalSeconds % 60;

    const parts: string[] = [];
    if (hours > 0) parts.push(`${hours}h`);
    if (minutes > 0) parts.push(`${minutes}m`);
    if (seconds > 0 || parts.length === 0) parts.push(`${seconds}s`);

    return parts.join(' ');
}

/**
 * Format seconds to MM:SS format
 */
function formatSecondsShort(seconds: number): string {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${String(secs).padStart(2, '0')}`;
}

/**
 * Create a simple lap table
 */
function createLapTable(laps: LapSummary[]): HTMLElement {
    const container = document.createElement('div');
    container.className = 'summary-laps';
    container.style.marginTop = '24px';
    container.style.overflowX = 'auto'; // Horizontal scroll for small screens

    if (laps.length === 0) return container;

    let html = `
        <h4 style="margin-bottom: 8px;">Laps</h4>
        <table class="lap-table" style="width: 100%; border-collapse: collapse; font-size: 0.9em;">
            <thead>
                <tr style="border-bottom: 2px solid var(--color-border);">
                    <th style="padding: 8px; text-align: left;">#</th>
                    <th style="padding: 8px; text-align: right;">Time</th>
                    <th style="padding: 8px; text-align: right;">Dist</th>
                    <th style="padding: 8px; text-align: right;">Pwr</th>
                    <th style="padding: 8px; text-align: right;">HR</th>
                    <th style="padding: 8px; text-align: right;">Cad</th>
                </tr>
            </thead>
            <tbody>
    `;

    laps.forEach((lap) => {
        const timeStr = formatSecondsShort(Math.round(lap.duration / 1000));
        const distStr = lap.distance > 0 ? `${(lap.distance / 1000).toFixed(2)}` : '-';

        html += `
            <tr style="border-bottom: 1px solid var(--color-border-light);">
                <td style="padding: 8px; text-align: left;">${lap.number}</td>
                <td style="padding: 8px; text-align: right;">${timeStr}</td>
                <td style="padding: 8px; text-align: right;">${distStr}</td>
                <td style="padding: 8px; text-align: right;">${lap.avgPower}</td>
                <td style="padding: 8px; text-align: right;">${lap.avgHeartRate}</td>
                <td style="padding: 8px; text-align: right;">${lap.avgCadence}</td>
            </tr>
        `;
    });

    html += `
            </tbody>
        </table>
    `;

    container.innerHTML = html;
    return container;
}

/**
 * Zone colors for the histogram - uses shared design tokens
 */
const ZONE_COLORS: Record<number, string> = POWER_ZONE_COLORS;

/**
 * Create zone distribution histogram chart
 */
function createZoneHistogram(distribution: ZoneDistribution, title: string): HTMLElement {
    const container = document.createElement('div');
    container.className = 'zone-distribution';

    // Filter zones with time > 0
    const activeZones = distribution.zones.filter((z) => z.timeInZoneMs > 0);

    if (activeZones.length === 0) {
        return container; // Return empty if no zone data
    }

    const maxTime = Math.max(...distribution.zones.map((z) => z.timeInZoneMs));

    container.innerHTML = `
        <h4 class="zone-distribution-title">${title}</h4>
        <div class="zone-chart">
            ${distribution.zones
                .map((zone) => {
                    const percent = maxTime > 0 ? (zone.timeInZoneMs / maxTime) * 100 : 0;
                    const timeSeconds = Math.round(zone.timeInZoneMs / 1000);
                    const color = zone.color || ZONE_COLORS[zone.zone] || '#888';
                    const timeStr = formatSecondsShort(timeSeconds);

                    return `
                    <div class="zone-bar-row" title="${zone.name}: ${timeStr}">
                        <span class="zone-bar-label">Z${zone.zone}</span>
                        <div class="zone-bar-container">
                            <div class="zone-bar" style="width: ${Math.max(percent, 2)}%; background-color: ${color};" aria-valuenow="${timeSeconds}" aria-label="${zone.name}"></div>
                        </div>
                        <span class="zone-bar-time">${timeStr}</span>
                    </div>
                `;
                })
                .join('')}
        </div>
    `;

    return container;
}

/**
 * Create workout summary content HTML element
 */
export function createSummaryContent(summary: WorkoutSummary, newRecords: string[] = []): HTMLElement {
    const container = document.createElement('div');
    container.className = 'workout-summary';

    const formatValue = (value: number | null, unit: string): string => {
        return value !== null ? `${value}${unit}` : '--';
    };

    // New Records Section
    let recordsHtml = '';
    if (newRecords.length > 0) {
        recordsHtml = `
            <div class="summary-records" style="background: rgba(255, 215, 0, 0.1); border: 1px solid var(--color-accent); border-radius: 8px; padding: 12px; margin-bottom: 16px;">
                <h4 style="margin: 0 0 8px 0; color: var(--color-accent); display: flex; align-items: center; gap: 8px;">
                     New Personal Records!
                </h4>
                <ul style="margin: 0; padding-left: 20px; list-style-type: none;">
                    ${newRecords.map((r) => `<li style="margin-bottom: 4px;"> ${r}</li>`).join('')}
                </ul>
            </div>
        `;
    }

    container.innerHTML = `
        ${recordsHtml}
        <div class="summary-duration">
            <span class="summary-label">Duration</span>
            <span class="summary-value">${formatDurationLong(summary.duration)}</span>
        </div>
        <div class="summary-grid">
            <div class="summary-metric">
                <span class="summary-metric-icon"></span>
                <span class="summary-metric-label">Power</span>
                <div class="summary-metric-values">
                    <span class="summary-avg">Avg: ${formatValue(summary.power.avg, 'W')}</span>
                    <span class="summary-max">Max: ${formatValue(summary.power.max, 'W')}</span>
                    ${summary.power.wattsPerKg ? `<span class="summary-wkg" style="display:block; font-size:0.85em; margin-top:4px; color:var(--color-text-secondary)">${summary.power.wattsPerKg} W/kg</span>` : ''}
                </div>
            </div>
            <div class="summary-metric">
                <span class="summary-metric-icon"></span>
                <span class="summary-metric-label">Heart Rate</span>
                <div class="summary-metric-values">
                    <span class="summary-avg">Avg: ${formatValue(summary.heartrate.avg, ' bpm')}</span>
                    <span class="summary-max">Max: ${formatValue(summary.heartrate.max, ' bpm')}</span>
                </div>
            </div>
            <div class="summary-metric">
                <span class="summary-metric-icon"></span>
                <span class="summary-metric-label">Cadence</span>
                <div class="summary-metric-values">
                    <span class="summary-avg">Avg: ${formatValue(summary.cadence.avg, ' rpm')}</span>
                    <span class="summary-max">Max: ${formatValue(summary.cadence.max, ' rpm')}</span>
                </div>
            </div>
        </div>
        ${
            summary.normalizedPower ||
            summary.intensityFactor ||
            summary.trainingLoad ||
            summary.variabilityIndex ||
            summary.work ||
            summary.vo2MaxEstimation
                ? `
        <div class="summary-advanced" style="margin-top: 12px; padding-top: 12px; border-top: 1px solid var(--color-border); display: grid; grid-template-columns: repeat(3, 1fr); gap: 8px; text-align: center;">
             ${
                 summary.normalizedPower
                     ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);">NP</div>
                <div style="font-weight: bold; font-size: 1.1em;">${summary.normalizedPower} W</div>
            </div>`
                     : ''
             }
            ${
                summary.intensityFactor
                    ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);">IF</div>
                <div style="font-weight: bold; font-size: 1.1em;">${summary.intensityFactor}</div>
            </div>`
                    : ''
            }
            ${
                summary.trainingLoad
                    ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);">TSS</div>
                <div style="font-weight: bold; font-size: 1.1em;">${summary.trainingLoad}</div>
            </div>`
                    : ''
            }
            ${
                summary.variabilityIndex
                    ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);">VI</div>
                <div style="font-weight: bold; font-size: 1.1em;">${summary.variabilityIndex}</div>
            </div>`
                    : ''
            }
            ${
                summary.work
                    ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);">Work</div>
                <div style="font-weight: bold; font-size: 1.1em;">${summary.work} kJ</div>
            </div>`
                    : ''
            }
            ${
                summary.vo2MaxEstimation
                    ? `
            <div class="advanced-metric">
                <div style="font-size: 0.8em; color: var(--color-text-secondary);" title="Estimated from Power/HR relationship">VO Max</div>
                <div style="font-weight: bold; font-size: 1.1em; color: var(--color-heart-rate);">${summary.vo2MaxEstimation}</div>
                <div style="font-size: 0.7em; color: var(--color-text-secondary);">ml/kg/min</div>
            </div>`
                    : ''
            }
        </div>
        `
                : ''
        }
        <div class="summary-samples">
            <span class="summary-samples-text">
                ${summary.power.count + summary.heartrate.count + summary.cadence.count} data points recorded
            </span>
        </div>
    `;

    // Add zone distribution charts if available
    if (summary.powerZoneDistribution && summary.powerZoneDistribution.totalTimeMs > 0) {
        const powerChart = createZoneHistogram(summary.powerZoneDistribution, ' Power Zone Distribution');
        container.appendChild(powerChart);
    }

    if (summary.hrZoneDistribution && summary.hrZoneDistribution.totalTimeMs > 0) {
        const hrChart = createZoneHistogram(summary.hrZoneDistribution, ' Heart Rate Zone Distribution');
        container.appendChild(hrChart);
    }

    // Add laps table
    if (summary.laps && summary.laps.length > 0) {
        const lapTable = createLapTable(summary.laps);
        container.appendChild(lapTable);
    }

    return container;
}

/**
 * Show post-workout summary modal
 *
 * @param summary - Workout summary data
 * @param callbacks - Action callbacks
 * @returns Promise resolving to the chosen action
 */
export function showWorkoutSummary(
    summary: WorkoutSummary,
    callbacks: {
        onDiscard: () => void;
        onKeepRecording: () => void;
        onFinish: () => void;
    },
    newRecords: string[] = []
): Promise<'discard' | 'keep' | 'finish'> {
    return new Promise((resolve) => {
        let closeModal: (() => void) | null = null;

        const content = createSummaryContent(summary, newRecords);

        closeModal = showModal({
            title: 'Workout Complete',
            content,
            icon: '',
            closeOnOverlay: false,
            buttons: [
                {
                    text: ' Discard',
                    variant: 'danger',
                    onClick: () => {
                        closeModal?.();
                        callbacks.onDiscard();
                        resolve('discard');
                    },
                },
                {
                    text: ' Continue',
                    variant: 'secondary',
                    onClick: () => {
                        closeModal?.();
                        callbacks.onKeepRecording();
                        resolve('keep');
                    },
                },
                {
                    text: ' Export Image',
                    variant: 'secondary',
                    onClick: () => {
                        downloadWorkoutImage(summary);
                    },
                },
                {
                    text: ' Finish & Save',
                    variant: 'primary',
                    onClick: () => {
                        closeModal?.();
                        callbacks.onFinish();
                        resolve('finish');
                    },
                },
            ],
            onClose: () => {
                callbacks.onKeepRecording();
                resolve('keep');
            },
        });
    });
}
