/**
 * Layer Manager
 *
 * Manages audio layers (loops) that are combined to create dynamic music.
 * Each layer can be activated/deactivated based on workout intensity.
 */

import { AudioGenerator } from '../generation/AudioGenerator.js';

/**
 * Represents a single musical layer (loop)
 */
export interface MusicLayer {
    /** Unique identifier for this layer */
    id: string;
    /** Pre-decoded audio buffer */
    audioBuffer: AudioBuffer | null;
    /** Minimum intensity (0-1) at which this layer should play */
    minIntensity: number;
    /** Maximum intensity (0-1) at which this layer should play */
    maxIntensity: number;
    /** Base volume for this layer (0-1) */
    baseVolume: number;
    /** Generator type ('bass', 'drums-low', etc.) */
    type: string;
    /** Music style ('rock', 'electronic', etc.) */
    style: string;
}

/**
 * Manages loading, playback, and mixing of audio layers
 */
export class LayerManager {
    private static bufferCache: Map<string, AudioBuffer> = new Map();
    private context: AudioContext;
    private layers: Map<string, MusicLayer> = new Map();
    private activeNodes: Map<string, { source: AudioBufferSourceNode; gain: GainNode }> = new Map();
    private masterGain: GainNode;
    private intensity: number = 0.5;
    private isPlaying: boolean = false;
    private startTime: number = 0;
    private generator: AudioGenerator;

    constructor(context: AudioContext, masterGain: GainNode) {
        this.context = context;
        this.masterGain = masterGain;
        this.generator = new AudioGenerator(context);
    }

    /**
     * Register a new layer
     *
     * @param layer - Layer definition
     */
    addLayer(layer: MusicLayer): void {
        this.layers.set(layer.id, layer);
    }

    /**
     * Replace all layers with new ones
     * Used when switching music styles
     */
    replaceAllLayers(newLayers: MusicLayer[]): void {
        this.stop();
        this.layers.clear();
        this.activeNodes.clear();

        for (const layer of newLayers) {
            this.addLayer(layer);
        }
    }

    /**
     * Load audio for a layer (using procedural generator)
     *
     * @param layerId - ID of the layer to load
     */
    async loadLayer(layerId: string): Promise<void> {
        const layer = this.layers.get(layerId);
        if (!layer) {
            throw new Error(`Layer not found: ${layerId}`);
        }

        if (layer.audioBuffer) {
            return; // Already loaded
        }

        const cacheKey = `${layer.type}-${layer.style}`;

        // Check global cache
        if (LayerManager.bufferCache.has(cacheKey)) {
            layer.audioBuffer = LayerManager.bufferCache.get(cacheKey)!;
            return;
        }

        try {
            // Generate audio buffer procedurally
            // Using setImmediate or wrapping in promise to yield to main thread if needed
            // But for now, sync is fine as it's fast enough
            const buffer = this.generator.generate(layer.type, layer.style);

            layer.audioBuffer = buffer;

            // Cache for reuse
            LayerManager.bufferCache.set(cacheKey, buffer);
        } catch (error) {
            console.error(`Failed to generate layer ${layerId}:`, error);
            throw error;
        }
    }

    /**
     * Load all registered layers
     */
    async loadAllLayers(): Promise<void> {
        const promises = Array.from(this.layers.keys()).map((id) => this.loadLayer(id));
        await Promise.all(promises);
    }

    /**
     * Start playing all active layers
     */
    start(): void {
        if (this.isPlaying) return;

        this.isPlaying = true;
        this.startTime = this.context.currentTime;
        this.updateLayers();
    }

    /**
     * Stop playing all layers
     */
    stop(): void {
        if (!this.isPlaying) return;

        this.isPlaying = false;

        // Stop all active nodes
        for (const [layerId] of this.activeNodes) {
            this.stopLayer(layerId, 0.5);
        }
    }

    /**
     * Update intensity and adjust active layers
     *
     * @param value - New intensity value (0-1)
     */
    setIntensity(value: number): void {
        this.intensity = Math.max(0, Math.min(1, value));
        if (this.isPlaying) {
            this.updateLayers();
        }
    }

    /**
     * Get current intensity
     */
    getIntensity(): number {
        return this.intensity;
    }

    /**
     * Update which layers should be playing based on current intensity
     */
    private updateLayers(): void {
        for (const [id, layer] of this.layers) {
            const shouldPlay =
                this.intensity >= layer.minIntensity && this.intensity <= layer.maxIntensity;

            if (shouldPlay && !this.activeNodes.has(id)) {
                this.startLayer(id);
            } else if (!shouldPlay && this.activeNodes.has(id)) {
                this.stopLayer(id, 1.0);
            }
        }
    }

    /**
     * Start playing a layer with fade-in
     *
     * @param layerId - ID of layer to start
     */
    private startLayer(layerId: string): void {
        const layer = this.layers.get(layerId);
        if (!layer || !layer.audioBuffer) {
            console.warn(`Cannot start layer ${layerId}: not loaded`);
            return;
        }

        // Create source node
        const source = this.context.createBufferSource();
        source.buffer = layer.audioBuffer;
        source.loop = true;

        // Create gain node for this layer
        const gain = this.context.createGain();
        gain.gain.value = 0; // Start silent

        // Connect: source -> gain -> master
        source.connect(gain);
        gain.connect(this.masterGain);

        // Store references
        this.activeNodes.set(layerId, { source, gain });

        // Calculate playback offset to sync with other layers
        const offset = this.isPlaying ? (this.context.currentTime - this.startTime) % layer.audioBuffer.duration : 0;

        // Start playback
        source.start(this.context.currentTime, offset);

        // Fade in
        const now = this.context.currentTime;
        gain.gain.setValueAtTime(0, now);
        gain.gain.linearRampToValueAtTime(layer.baseVolume, now + 1.0);

        // Handle source ending (shouldn't happen with loop=true, but be safe)
        source.onended = () => {
            if (this.activeNodes.has(layerId)) {
                this.activeNodes.delete(layerId);
            }
        };
    }

    /**
     * Stop playing a layer with fade-out
     *
     * @param layerId - ID of layer to stop
     * @param fadeDuration - Fade-out duration in seconds
     */
    private stopLayer(layerId: string, fadeDuration: number = 1.0): void {
        const nodes = this.activeNodes.get(layerId);
        if (!nodes) return;

        const { source, gain } = nodes;
        const now = this.context.currentTime;

        // Fade out
        gain.gain.cancelScheduledValues(now);
        gain.gain.setValueAtTime(gain.gain.value, now);
        gain.gain.linearRampToValueAtTime(0, now + fadeDuration);

        // Stop source after fade
        source.stop(now + fadeDuration);

        // Remove from active nodes
        this.activeNodes.delete(layerId);
    }

    /**
     * Set master volume
     *
     * @param volume - Volume level (0-1)
     */
    setVolume(volume: number): void {
        const targetVolume = Math.max(0, Math.min(1, volume));
        const now = this.context.currentTime;
        this.masterGain.gain.cancelScheduledValues(now);
        this.masterGain.gain.setValueAtTime(this.masterGain.gain.value, now);
        this.masterGain.gain.linearRampToValueAtTime(targetVolume, now + 0.1);
    }

    /**
     * Check if currently playing
     */
    isCurrentlyPlaying(): boolean {
        return this.isPlaying;
    }

    /**
     * Clean up resources
     */
    dispose(): void {
        this.stop();
        this.layers.clear();
        this.activeNodes.clear();
    }
}
