import { createFetchSSE, type SSEConnection } from '../api/streamClient.js';
import type { StreamerNotification } from '../types/stream.js';

type ReactionCallback = (notification: StreamerNotification) => void;

export class ReactionListener {
    private connection: SSEConnection | null = null;
    private callbacks: Set<ReactionCallback> = new Set();
    private streamName: string | null = null;
    private reconnectAttempts: number = 0;
    private maxReconnectAttempts: number = 5;
    private reconnectDelay: number = 2000;

    async start(streamName: string): Promise<void> {
        this.streamName = streamName;
        this.reconnectAttempts = 0;
        await this.connect();
    }

    stop(): void {
        if (this.connection) {
            this.connection.close();
            this.connection = null;
        }
        this.streamName = null;
        this.reconnectAttempts = 0;
    }

    onReaction(callback: ReactionCallback): () => void {
        this.callbacks.add(callback);
        return () => this.callbacks.delete(callback);
    }

    private async connect(): Promise<void> {
        if (!this.streamName) return;

        const apiUrl = import.meta.env.VITE_API_URL || '';
        const url = `${apiUrl}/api/streams/${this.streamName}/reactions/listen`;

        this.connection = createFetchSSE(
            url,
            (data: any) => this.handleMessage(data),
            () => console.log('Connected to reaction stream'),
            (error: unknown) => this.handleError(error as Error)
        );
    }

    private handleMessage(data: unknown): void {
        const message = data as {
            type: string;
            id: string;
            data: {
                type: 'emoji' | 'text' | 'cheer';
                content: string;
                viewerId: string;
                viewerName?: string;
                timestamp: string;
            };
        };

        if (message.type === 'reaction') {
            const notification: StreamerNotification = {
                id: message.id,
                type: message.data.type,
                content: message.data.content,
                viewerId: message.data.viewerId,
                viewerName: message.data.viewerName,
                timestamp: parseInt(message.data.timestamp),
            };

            this.callbacks.forEach((cb) => cb(notification));
        }
    }

    private handleError(error: Error): void {
        console.error('Reaction listener error:', error);

        if (this.reconnectAttempts < this.maxReconnectAttempts && this.streamName) {
            this.reconnectAttempts++;
            const delay = this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1);
            setTimeout(() => this.connect(), delay);
        }
    }
}
