import { View } from './View.js';
import { IRouter } from './types.js';
import { Capacitor } from '@capacitor/core';
import { App } from '@capacitor/app';

type ViewLoader = () => Promise<View>;

interface Route {
    path: string;
    viewId: string;
}

/**
 * Client-side router for single-page application navigation.
 * 
 * Features:
 * - Dual-mode routing: History API (web) and hash-based routing (native)
 * - Lazy loading of views for optimal performance
 * - View lifecycle management (init, onEnter, onLeave)
 * - Hardware back button support (native platforms)
 * - 404 error handling
 * 
 * @example
 * ```typescript
 * const router = new Router(document.getElementById('app'));
 * 
 * // Register views
 * router.registerView(new DashboardView());
 * router.registerLazyView('settings', () => 
 *   import('./SettingsView.js').then(m => new m.SettingsView())
 * );
 * 
 * // Add routes
 * router.addRoute('/', 'dashboard');
 * router.addRoute('/settings', 'settings');
 * 
 * // Start router
 * router.start();
 * 
 * // Navigate programmatically
 * router.navigate('/settings');
 * ```
 */
export class Router implements IRouter {
    private views: Map<string, View> = new Map();
    private lazyViews: Map<string, ViewLoader> = new Map();
    private routes: Route[] = [];
    private currentViewId: string | null = null;
    private container: HTMLElement;
    private isNative: boolean;

    /**
     * Creates a new Router instance.
     * 
     * @param container - DOM element that will contain view content
     * 
     * @remarks
     * The constructor automatically:
     * - Detects platform (web vs native)
     * - Sets up browser history listeners (popstate)
     * - Configures hash change listeners (native only)
     * - Registers hardware back button handler (native only)
     */
    constructor(container: HTMLElement) {
        this.container = container;
        this.isNative = Capacitor.isNativePlatform();

        // Handle browser back/forward buttons and hash changes
        window.addEventListener('popstate', () => {
            this.handleLocationChange();
        });

        // Also listen for hash changes (for Capacitor/native)
        if (this.isNative) {
            window.addEventListener('hashchange', () => {
                this.handleLocationChange();
            });
        }

        // Hardware back button handling for Capacitor
        if (this.isNative) {
            App.addListener('backButton', ({ canGoBack }) => {
                if (canGoBack) {
                    window.history.back();
                } else {
                    App.exitApp();
                }
            });
        }
    }

    /**
     * Register a view with the router (eager loading).
     * 
     * @param view - View instance implementing the View interface
     * 
     * @remarks
     * - Creates a container div with id `page-{viewId}` if it doesn't exist
     * - Calls view.init() immediately with the container
     * - View is hidden by default (display: none)
     * 
     * @example
     * ```typescript
     * router.registerView(new DashboardView());
     * ```
     */
    public registerView(view: View): void {
        this.views.set(view.id, view);

        // Find existing container or create one
        let viewContainer = document.getElementById(`page-${view.id}`);

        if (!viewContainer) {
            viewContainer = document.createElement('div');
            viewContainer.id = `page-${view.id}`;
            viewContainer.className = 'page-view';
            viewContainer.style.display = 'none'; // Hidden by default
            this.container.appendChild(viewContainer);
        }

        // Initialize the view with its container
        view.init(viewContainer);
    }

    /**
     * Register a lazy-loaded view that will be imported on first access.
     * 
     * @param viewId - Unique identifier for the view
     * @param loader - Async function that imports and returns a View instance
     * 
     * @remarks
     * Use this for code splitting and performance optimization.
     * The loader is only called when the user first navigates to this view.
     * 
     * @example
     * ```typescript
     * router.registerLazyView('settings', () => 
     *   import('./views/SettingsView.js').then(m => new m.SettingsView())
     * );
     * ```
     */
    public registerLazyView(viewId: string, loader: ViewLoader): void {
        this.lazyViews.set(viewId, loader);
    }

    /**
     * Add a route mapping from path to view.
     * 
     * @param path - URL path (e.g., '/', '/settings', '/history')
     * @param viewId - View identifier matching a registered view
     * 
     * @remarks
     * - Paths are matched exactly (no regex or wildcards)
     * - Order doesn't matter (no precedence)
     * - Unknown paths will show 404 view if registered
     * 
     * @example
     * ```typescript
     * router.addRoute('/', 'dashboard');
     * router.addRoute('/settings', 'settings');
     * ```
     */
    public addRoute(path: string, viewId: string): void {
        this.routes.push({ path, viewId });
    }

    /**
     * Navigate to a specific path.
     * 
     * @param path - URL path to navigate to
     * @param replace - If true, replaces current history entry instead of adding new one
     * 
     * @remarks
     * Platform-specific behavior:
     * - Web: Uses History API (pushState/replaceState)
     * - Native: Uses hash-based routing (#/path)
     * 
     * Triggers view lifecycle:
     * 1. currentView.onLeave()
     * 2. newView.onEnter()
     * 3. Dispatches 'route-changed' event
     * 
     * @example
     * ```typescript
     * router.navigate('/settings');           // Add to history
     * router.navigate('/dashboard', true);    // Replace history
     * ```
     */
    public navigate(path: string, replace = false): void {
        if (this.isNative) {
            // Use hash-based routing on Capacitor/native for file:// compatibility
            if (replace) {
                window.location.replace('#' + path);
            } else {
                window.location.hash = path;
            }
        } else {
            // Use History API for web browsers
            if (replace) {
                window.history.replaceState({}, '', path);
            } else {
                window.history.pushState({}, '', path);
            }
            this.handleLocationChange();
        }
    }

    /**
     * Start the router and navigate to the initial view.
     * 
     * @remarks
     * Call this once after all views and routes are registered.
     * 
     * On native platforms:
     * - If no hash exists, defaults to '/' (root)
     * - Otherwise uses current hash
     * 
     * On web platforms:
     * - Uses current pathname
     * 
     * @example
     * ```typescript
     * router.start(); // Navigate to current URL or default view
     * ```
     */
    public start(): void {
        if (this.isNative) {
            // On native, start with initial hash check
            if (!window.location.hash) {
                // If no hash, default to root
                window.location.hash = '/';
            }
        }
        this.handleLocationChange();
    }

    /**
     * Navigate back in browser history.
     * 
     * @remarks
     * Triggers popstate event which calls handleLocationChange().
     * On native platforms, hardware back button also calls this.
     * 
     * @example
     * ```typescript
     * router.back(); // Go to previous page
     * ```
     */
    public back(): void {
        window.history.back();
    }

    /**
     * Handle location changes from browser navigation or navigate() calls.
     * 
     * @remarks
     * Internal method called by:
     * - popstate event (back/forward buttons)
     * - hashchange event (native platforms)
     * - navigate() method (web platforms)
     * 
     * Flow:
     * 1. Extract path from URL (pathname or hash)
     * 2. Match path to route
     * 3. Show 404 if no match (or fallback to root)
     * 4. Switch to matched view
     */
    private async handleLocationChange(): Promise<void> {
        let path: string;

        if (this.isNative) {
            // On native platforms, use hash-based routing
            // Hash format: #/path -> extract '/path'
            const hash = window.location.hash;
            path = hash ? hash.slice(1) : '/';
            // Normalize empty hash to root
            if (!path || path === '') {
                path = '/';
            }
        } else {
            // On web, use pathname
            path = window.location.pathname;
            // Normalize /index.html to /
            if (path.endsWith('/index.html') || path.endsWith('.html')) {
                path = '/';
            }
        }

        // Simple matching logic
        let matchedRoute = this.routes.find((r) => r.path === path);

        // Handle unknown routes by showing 404 view
        if (!matchedRoute) {
            // Check if we have a NotFound view registered
            const notFoundRoute = this.routes.find((r) => r.viewId === 'not-found');
            if (notFoundRoute) {
                await this.switchView('not-found');
            } else {
                // Fallback to root if no 404 view exists
                matchedRoute = this.routes.find((r) => r.path === '/') || this.routes[0];
                if (matchedRoute) {
                    await this.switchView(matchedRoute.viewId);
                }
            }
            return;
        }

        if (matchedRoute) {
            await this.switchView(matchedRoute.viewId);
        }
    }

    /**
     * Switch the currently visible view.
     * 
     * @param viewId - ID of the view to switch to
     * 
     * @remarks
     * Handles the complete view transition:
     * 1. Skip if already on target view
     * 2. Lazy load view if not yet loaded (shows loading indicator)
     * 3. Call currentView.onLeave()
     * 4. Hide current view (display: none)
     * 5. Show new view (display: '')
     * 6. Call newView.onEnter()
     * 7. Dispatch 'route-changed' event for NavBar, etc.
     * 
     * @throws Logs error if view load fails, stays on current view
     */
    private async switchView(viewId: string): Promise<void> {
        if (this.currentViewId === viewId) return;

        // Check if loading is needed
        if (!this.views.has(viewId)) {
            const loader = this.lazyViews.get(viewId);
            if (loader) {
                // Show loading indicator
                this.showLoadingIndicator();

                try {
                    const view = await loader();
                    this.registerView(view);
                } catch (error) {
                    console.error(`Failed to load view for ${viewId}:`, error);
                    this.hideLoadingIndicator();
                    return;
                } finally {
                    this.hideLoadingIndicator();
                }
            } else {
                console.warn(`View ${viewId} not registered.`);
                return;
            }
        }

        // Leave current view
        if (this.currentViewId) {
            const currentView = this.views.get(this.currentViewId);
            if (currentView) {
                currentView.onLeave();
                const el = document.getElementById(`page-${this.currentViewId}`);
                if (el) {
                    // Fade out animation
                    el.classList.add('fade-out');
                    await new Promise((resolve) => setTimeout(resolve, 200));
                    el.style.display = 'none';
                    el.classList.remove('fade-out');
                }
            }
        }

        // Enter new view
        const newView = this.views.get(viewId);
        if (newView) {
            const el = document.getElementById(`page-${viewId}`);
            if (el) {
                // Fade in animation
                el.style.opacity = '0';
                el.style.display = '';
                // Force reflow
                void el.offsetHeight;
                requestAnimationFrame(() => {
                    el.style.opacity = '1';
                });
            }
            newView.onEnter();
            this.currentViewId = viewId;

            // Dispatch event for UI updates (e.g. Nav Bar)
            window.dispatchEvent(new CustomEvent('route-changed', { detail: { viewId } }));
        }
    }

    /**
     * Show loading indicator overlay
     */
    private showLoadingIndicator(): void {
        const loader = document.getElementById('route-loader');
        if (loader) {
            loader.style.display = 'flex';
        }
    }

    /**
     * Hide loading indicator overlay
     */
    private hideLoadingIndicator(): void {
        const loader = document.getElementById('route-loader');
        if (loader) {
            loader.style.display = 'none';
        }
    }
}
