export class WorkerTimer {
    private worker: Worker;
    private callbacks: Map<number, () => void> = new Map();
    private nextId: number = 1;

    constructor() {
        this.worker = new Worker(new URL('./timer.worker.ts', import.meta.url), {
            type: 'module',
        });

        this.worker.onmessage = (e) => {
            const { type, id } = e.data;
            if (type === 'tick' && this.callbacks.has(id)) {
                this.callbacks.get(id)!();
            }
        };
    }

    public setInterval(callback: () => void, interval: number): number {
        const id = this.nextId++;
        this.callbacks.set(id, callback);
        this.worker.postMessage({ type: 'start', id, interval });
        return id;
    }

    public clearInterval(id: number): void {
        if (this.callbacks.has(id)) {
            this.callbacks.delete(id);
            this.worker.postMessage({ type: 'stop', id });
        }
    }

    public terminate(): void {
        this.worker.terminate();
        this.callbacks.clear();
    }
}

export const workerTimer = new WorkerTimer();
