import { openDB, type IDBPDatabase } from 'idb';
import type { PersonalRecord, PowerCurve } from '../types/personal-records.js';

export interface RecordsData {
    allTime: PowerCurve;
    /**
     * Records by year (season)
     */
    byYear: Record<number, PowerCurve>;
}

const DEFAULT_DATA: RecordsData = {
    allTime: {},
    byYear: {},
};

const DB_NAME = 'bpt-records';
const STORE_NAME = 'personal-records';
const RECORD_KEY = 'user-records';

export class PersonalRecordsStore {
    private data: RecordsData = JSON.parse(JSON.stringify(DEFAULT_DATA));
    private dbPromise: Promise<IDBPDatabase> | null = null;
    private listeners: ((data: RecordsData) => void)[] = [];
    private initialized = false;

    constructor() {
        if (typeof window !== 'undefined') {
            this.init();
        }
    }

    private async init() {
        try {
            this.dbPromise = openDB(DB_NAME, 1, {
                upgrade(db) {
                    db.createObjectStore(STORE_NAME);
                },
            });
            await this.load();
            this.initialized = true;
        } catch (error) {
            console.error('Failed to initialize PersonalRecordsStore:', error);
        }
    }

    private async load() {
        if (!this.dbPromise) return;
        try {
            const db = await this.dbPromise;
            const stored = await db.get(STORE_NAME, RECORD_KEY);
            if (stored) {
                this.data = stored;
                this.notifyListeners();
            }
        } catch (error) {
            console.error('Failed to load records:', error);
        }
    }

    private async persist() {
        if (!this.dbPromise) return;
        try {
            const db = await this.dbPromise;
            await db.put(STORE_NAME, this.data, RECORD_KEY);
        } catch (error) {
            console.error('Failed to save records:', error);
        }
    }

    /**
     * Check if a value is a new record and update if so
     * @returns { isAllTime: boolean, isSeason: boolean }
     */
    public async checkAndAddRecord(
        duration: number,
        value: number,
        timestamp: number,
        workoutId?: string
    ): Promise<{ isAllTime: boolean; isSeason: boolean }> {
        let isAllTime = false;
        let isSeason = false;
        const year = new Date(timestamp).getFullYear();

        // Check All Time
        const currentAllTime = this.data.allTime[duration];
        if (!currentAllTime || value > currentAllTime.value) {
            this.data.allTime[duration] = { value, date: timestamp, workoutId };
            isAllTime = true;
        }

        // Check Season
        if (!this.data.byYear[year]) {
            this.data.byYear[year] = {};
        }
        const currentSeason = this.data.byYear[year][duration];
        if (!currentSeason || value > currentSeason.value) {
            this.data.byYear[year][duration] = { value, date: timestamp, workoutId };
            isSeason = true;
        }

        if (isAllTime || isSeason) {
            await this.persist();
            this.notifyListeners();
        }

        return { isAllTime, isSeason };
    }

    public getRecord(duration: number, year?: number): PersonalRecord | undefined {
        if (year) {
            return this.data.byYear[year]?.[duration];
        }
        return this.data.allTime[duration];
    }

    public getAllTimeCurve(): PowerCurve {
        return { ...this.data.allTime };
    }

    public getSeasonCurve(year: number = new Date().getFullYear()): PowerCurve {
        return { ...(this.data.byYear[year] || {}) };
    }

    public subscribe(listener: (data: RecordsData) => void): () => void {
        this.listeners.push(listener);
        // Invoke immediately with current data
        if (this.initialized) {
            listener(this.data);
        }
        return () => {
            const index = this.listeners.indexOf(listener);
            if (index !== -1) {
                this.listeners.splice(index, 1);
            }
        };
    }

    private notifyListeners() {
        for (const listener of this.listeners) {
            listener(this.data);
        }
    }
}

export const personalRecordsStore = new PersonalRecordsStore();
