/**
 * Live Data Values Bus
 *
 * Lightweight event bus for streaming live data field values to UI components
 * that don't have direct access to the DataFieldsManager (like the FieldPicker).
 *
 * @module data-fields/live-values
 */

type ValueListener = (value: number | null) => void;

// Store listeners by field ID
const listeners = new Map<string, Set<ValueListener>>();

/**
 * Publish a new value for a data field
 * @param fieldId - The ID of the field
 * @param value - The new value (number or null)
 */
export function publishFieldValue(fieldId: string, value: number | null): void {
    const fieldListeners = listeners.get(fieldId);
    if (fieldListeners) {
        for (const listener of fieldListeners) {
            try {
                listener(value);
            } catch (err) {
                console.error(`Error in value listener for ${fieldId}:`, err);
            }
        }
    }
}

/**
 * Subscribe to updates for a specific data field
 * @param fieldId - The ID of the field to observe
 * @param listener - Callback function receiving new values
 * @returns Unsubscribe function
 */
export function subscribeToFieldValue(fieldId: string, listener: ValueListener): () => void {
    if (!listeners.has(fieldId)) {
        listeners.set(fieldId, new Set());
    }

    const fieldListeners = listeners.get(fieldId)!;
    fieldListeners.add(listener);

    // Return cleanup function
    return () => {
        fieldListeners.delete(listener);
        if (fieldListeners.size === 0) {
            listeners.delete(fieldId);
        }
    };
}
