/**
 * BPT Client - Main Entry Point
 *
 * Progressive Web App for tracking cycling workouts with Bluetooth sensors.
 *
 * This module initializes all application features:
 * - Bluetooth sensor connections (power, cadence, heart rate)
 * - Real-time metrics display
 * - Workout timer
 * - Data export (JSON, TCX, CSV)
 * - Live streaming
 * - PWA features (service worker, install prompt)
 *
 * @module main
 *
 * @description
 * The app uses a state-based architecture with three main state objects:
 * - measurementsState: Tracks workout data (power, cadence, HR measurements)
 * - connectionsState: Tracks Bluetooth connection status
 * - timeState: Tracks recording state and elapsed time
 *
 * @example
 * // State is initialized automatically on app load
 * // Access exposed state in tests:
 * window.measurementsState
 * window.connectionsState
 */

import './styles/main.css';
import './styles/race-predictor.css';
import './styles/vo2max.css';

// Register Web Components using lazy loader
import { registerLazyComponent } from './components/lazyLoader.js';

// Base Components
registerLazyComponent('bpt-metric-display', () => import('./components/MetricDisplay.js'));
registerLazyComponent('bpt-power-gauge', () => import('./components/PowerGauge.js'));
registerLazyComponent('bpt-zone-gauge', () => import('./components/ZoneGauge.js'));
registerLazyComponent('bpt-live-chart', () => import('./components/LiveChart.js'));
registerLazyComponent('bpt-workout-timer', () => import('./components/WorkoutTimer.js'));
registerLazyComponent('bpt-toast', () => import('./components/Toast.js'));
registerLazyComponent('bpt-modal', () => import('./components/Modal.js'));
registerLazyComponent('bpt-target-visualizer', () => import('./components/TargetVisualizer.js'));
registerLazyComponent('bpt-workout-profile', () => import('./components/WorkoutProfile.js'));

// Data Fields Components
registerLazyComponent('bpt-data-field', () => import('./components/data-fields/DataFieldComponent.js'));
registerLazyComponent('bpt-data-screen', () => import('./components/data-fields/DataScreenComponent.js'));
registerLazyComponent('bpt-screen-carousel', () => import('./components/data-fields/ScreenCarouselComponent.js'));
registerLazyComponent('bpt-field-picker', () => import('./components/data-fields/FieldPickerComponent.js'));
registerLazyComponent('bpt-screen-editor', () => import('./components/data-fields/ScreenEditorComponent.js'));
registerLazyComponent('bpt-profile-settings', () => import('./components/data-fields/ProfileSettingsComponent.js'));

import './monitoring/performance.js';
import './services/PersonalRecordsManager.js';
import { initializeState } from './state/StateProvider.js';
import { exposeVariablesDuringTest } from './exposeVariablesDuringTest.js';
import { setupRouter } from './init/router.js';
import { initUi } from './init/ui.js';
import { initHardware } from './init/hardware.js';
import { getSettings } from './config/settings.js';
import './ui/navbar.css';
import './ui/map.css';

const initApp = (): void => {
    try {
        // Performance monitoring initialized via import

        // Sync mockSensors setting with window flag for bluetooth factory
        // This must happen before any sensor connections are attempted
        const settings = getSettings();
        (window as any).useMockSensors = settings.mockSensors;

        /**
         * Initialize application state
         */
        const appState = initializeState();
        const { measurementsState, connectionsState, timeState } = appState;

        // Expose state for E2E testing
        exposeVariablesDuringTest({ measurementsState, connectionsState });

        // Initialize Hardware (Sensors & Streaming)
        const { streamManager, connectionManager } = initHardware({ measurementsState, connectionsState, timeState });

        // Initialize UI
        initUi({ measurementsState, timeState, connectionsState, streamManager, connectionManager });

        // Initialize Navigation Logic
        const router = setupRouter();
        router.start();

        // Expose router to window for global access (navigation)
        // Using explicit type compatible with window expansion
        (window as unknown as { router: typeof router }).router = router;
    } catch (error: unknown) {
        console.error('Failed to initialize app:', error);
    }
};

// Initialize app when DOM is ready
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', initApp, { once: true });
} else {
    initApp();
}

console.log('Bike Power Tracker initialized');
