import { Chart, type ChartConfiguration } from 'chart.js/auto';
import { personalRecordsStore } from '../state/PersonalRecordsStore.js';

/**
 * Render Power Curve Comparison Chart
 * Compares specific workout curve against All-Time and Season bests
 */
export function renderPowerCurveComparison(
    canvasId: string,
    workoutCurve: { duration: number; watts: number }[]
): Chart | null {
    const canvas = document.getElementById(canvasId) as HTMLCanvasElement;
    if (!canvas) return null;

    // Get comparison data
    const allTime = personalRecordsStore.getAllTimeCurve();
    const season = personalRecordsStore.getSeasonCurve();

    // Collect all unique durations to ensure x-axis covers everything
    const workoutDurations = workoutCurve.map((p) => p.duration);
    const allTimeDurations = Object.keys(allTime).map(Number);
    const seasonDurations = Object.keys(season).map(Number);

    const allDurations = Array.from(new Set([...workoutDurations, ...allTimeDurations, ...seasonDurations])).sort(
        (a, b) => a - b
    );

    // Filter to relevant range (e.g. up to workout max duration + a bit, or just standard set)
    // We only care about durations that exist in the workout or correspond to standard points
    // But displaying the full curve context is nice.

    // Map data to the sorted durations
    const workoutData = allDurations.map((d) => {
        const point = workoutCurve.find((p) => p.duration === d);
        return point ? point.watts : null;
    });

    const allTimeData = allDurations.map((d) => allTime[d]?.value || null);
    const seasonData = allDurations.map((d) => season[d]?.value || null);

    const formatDurationLabel = (seconds: number): string => {
        if (seconds < 60) return `${seconds}s`;
        if (seconds < 3600) return `${Math.floor(seconds / 60)}m`;
        return `${(seconds / 3600).toFixed(1)}h`;
    };

    const labels = allDurations.map(formatDurationLabel);

    // Destroy existing chart content if any (though typically we create fresh canvas in modal/view)
    // If reusing canvas, we'd need to destroy old chart instance.
    // Ideally the calling code handles cleanup or we attach it to the canvas element.
    const existingChart = Chart.getChart(canvas);
    if (existingChart) {
        existingChart.destroy();
    }

    const config: ChartConfiguration = {
        type: 'line',
        data: {
            labels,
            datasets: [
                {
                    label: 'This Workout',
                    data: workoutData,
                    borderColor: '#ef4444', // Red-500
                    backgroundColor: 'rgba(239, 68, 68, 0.1)',
                    borderWidth: 3,
                    tension: 0.1,
                    pointRadius: 4,
                    fill: false,
                    order: 1, // Top layer
                },
                {
                    label: 'Season Best',
                    data: seasonData,
                    borderColor: '#3b82f6', // Blue-500
                    backgroundColor: 'rgba(59, 130, 246, 0.1)',
                    borderWidth: 2,
                    borderDash: [5, 5],
                    tension: 0.1,
                    pointRadius: 3,
                    fill: false,
                    order: 2,
                },
                {
                    label: 'All-Time Best',
                    data: allTimeData,
                    borderColor: '#f59e0b', // Amber-500
                    backgroundColor: 'rgba(245, 158, 11, 0.1)',
                    borderWidth: 2,
                    borderDash: [2, 2],
                    tension: 0.1,
                    pointRadius: 2,
                    fill: false,
                    order: 3,
                },
            ],
        },
        options: {
            responsive: true,
            maintainAspectRatio: false,
            interaction: {
                mode: 'index',
                intersect: false,
            },
            plugins: {
                legend: {
                    position: 'top',
                    labels: { color: getComputedStyle(document.body).getPropertyValue('--color-text-primary') },
                },
                tooltip: {
                    callbacks: {
                        label: (ctx: any) => {
                            if (ctx.raw === null) return '';
                            return `${ctx.dataset.label}: ${Math.round(ctx.parsed.y)}W`;
                        },
                    },
                },
                title: {
                    display: true,
                    text: 'Power Curve Comparison',
                    color: getComputedStyle(document.body).getPropertyValue('--color-text-primary'),
                },
            },
            scales: {
                x: {
                    grid: { color: 'rgba(128, 128, 128, 0.1)' },
                    ticks: { color: getComputedStyle(document.body).getPropertyValue('--color-text-secondary') },
                    title: { display: true, text: 'Duration' },
                },
                y: {
                    beginAtZero: true,
                    title: { display: true, text: 'Power (Watts)' },
                    grid: { color: 'rgba(128, 128, 128, 0.1)' },
                    ticks: { color: getComputedStyle(document.body).getPropertyValue('--color-text-secondary') },
                },
            },
        },
    };

    return new Chart(canvas, config);
}
