/**
 * Audio Effects Module
 *
 * Provides audio processing effects for music generation:
 * - Low-pass filter for intensity control
 * - Reverb (convolver) for spatial depth
 * - Distortion for energy/aggression
 */

/**
 * Create a low-pass filter node
 *
 * @param context - AudioContext instance
 * @param frequency - Cutoff frequency in Hz (20-20000)
 * @returns BiquadFilterNode configured as low-pass
 */
export function createLowPassFilter(context: AudioContext, frequency: number = 1000): BiquadFilterNode {
    const filter = context.createBiquadFilter();
    filter.type = 'lowpass';
    filter.frequency.value = Math.max(20, Math.min(20000, frequency));
    filter.Q.value = 1; // Resonance
    return filter;
}

/**
 * Create a simple distortion effect using WaveShaper
 *
 * @param context - AudioContext instance
 * @param amount - Distortion amount (0-1)
 * @returns WaveShaperNode with distortion curve
 */
export function createDistortion(context: AudioContext, amount: number = 0): WaveShaperNode {
    const shaper = context.createWaveShaper();
    const curve = makeDistortionCurve(amount);
    shaper.curve = curve;
    shaper.oversample = '2x';
    return shaper;
}

/**
 * Generate a distortion curve for WaveShaper
 *
 * @param amount - Distortion amount (0-1)
 * @returns Float32Array distortion curve
 */
function makeDistortionCurve(amount: number): Float32Array<ArrayBuffer> {
    const k = amount * 100;
    const samples = 44100;
    const curve = new Float32Array(samples) as Float32Array<ArrayBuffer>;
    const deg = Math.PI / 180;

    for (let i = 0; i < samples; i++) {
        const x = (i * 2) / samples - 1;
        curve[i] = ((3 + k) * x * 20 * deg) / (Math.PI + k * Math.abs(x));
    }

    return curve;
}

/**
 * Create a gain node for volume control
 *
 * @param context - AudioContext instance
 * @param initialGain - Initial gain value (0-1+)
 * @returns GainNode
 */
export function createGainNode(context: AudioContext, initialGain: number = 1): GainNode {
    const gain = context.createGain();
    gain.gain.value = initialGain;
    return gain;
}

/**
 * Smoothly transition a gain node to a new value
 *
 * @param gainNode - GainNode to modify
 * @param targetValue - Target gain value
 * @param durationSeconds - Transition duration in seconds
 */
export function fadeGain(gainNode: GainNode, targetValue: number, durationSeconds: number = 0.5): void {
    const now = gainNode.context.currentTime;
    gainNode.gain.cancelScheduledValues(now);
    gainNode.gain.setValueAtTime(gainNode.gain.value, now);
    gainNode.gain.linearRampToValueAtTime(targetValue, now + durationSeconds);
}

/**
 * Create a dynamics compressor for evening out audio levels
 *
 * @param context - AudioContext instance
 * @returns DynamicsCompressorNode with music-appropriate settings
 */
export function createCompressor(context: AudioContext): DynamicsCompressorNode {
    const compressor = context.createDynamicsCompressor();
    compressor.threshold.value = -24; // dB
    compressor.knee.value = 30;
    compressor.ratio.value = 12;
    compressor.attack.value = 0.003; // seconds
    compressor.release.value = 0.25; // seconds
    return compressor;
}

/**
 * Duck a gain node (reduce volume temporarily)
 *
 * @param gainNode - GainNode to duck
 * @param duckLevel - Level to duck to (0-1)
 * @param duckDuration - How long to stay ducked (ms)
 * @param fadeDuration - Fade in/out duration (seconds)
 */
export function duckGain(
    gainNode: GainNode,
    duckLevel: number = 0.2,
    duckDuration: number = 2000,
    fadeDuration: number = 0.2
): void {
    const now = gainNode.context.currentTime;
    const originalValue = gainNode.gain.value;

    // Cancel any scheduled changes
    gainNode.gain.cancelScheduledValues(now);

    // Duck down
    gainNode.gain.setValueAtTime(originalValue, now);
    gainNode.gain.linearRampToValueAtTime(duckLevel, now + fadeDuration);

    // Restore after duration
    const restoreTime = now + fadeDuration + duckDuration / 1000;
    gainNode.gain.setValueAtTime(duckLevel, restoreTime);
    gainNode.gain.linearRampToValueAtTime(originalValue, restoreTime + fadeDuration * 2);
}
