import type { WorkoutSummary } from '../calculations/summary.js';
import { formatDurationLong } from './modal.js';

/**
 * Generate a shareable image for the workout summary.
 *
 * Draws the stats onto a canvas and returns a Blob.
 *
 * @param summary - The workout summary data
 * @returns Promise resolving to a Blob representing the PNG image
 */
export async function generateShareImage(summary: WorkoutSummary): Promise<Blob | null> {
    const width = 1080;
    const height = 1350; // 4:5 aspect ratio (good for Instagram/Socials)

    const canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext('2d');

    if (!ctx) return null;

    // --- Colors ---
    const bgMain = '#121212';
    const bgCard = '#1E1E1E';
    const textPrimary = '#FFFFFF';
    const textSecondary = '#AAAAAA';
    const accent = '#2196F3';
    const powerColor = '#FFC107'; // Amber
    const hrColor = '#F44336'; // Red

    // --- Helper Functions ---
    const drawRoundedRect = (x: number, y: number, w: number, h: number, r: number, fill: string) => {
        ctx.beginPath();
        ctx.moveTo(x + r, y);
        ctx.lineTo(x + w - r, y);
        ctx.quadraticCurveTo(x + w, y, x + w, y + r);
        ctx.lineTo(x + w, y + h - r);
        ctx.quadraticCurveTo(x + w, y + h, x + w - r, y + h);
        ctx.lineTo(x + r, y + h);
        ctx.quadraticCurveTo(x, y + h, x, y + h - r);
        ctx.lineTo(x, y + r);
        ctx.quadraticCurveTo(x, y, x + r, y);
        ctx.closePath();
        ctx.fillStyle = fill;
        ctx.fill();
    };

    const drawText = (
        text: string,
        x: number,
        y: number,
        fontSize: number,
        color: string,
        align: CanvasTextAlign = 'left',
        bold = false
    ) => {
        ctx.fillStyle = color;
        ctx.font = `${bold ? 'bold ' : ''}${fontSize}px sans-serif`;
        ctx.textAlign = align;
        ctx.fillText(text, x, y);
    };

    const drawMetricCard = (
        label: string,
        value: string,
        subValue: string | null,
        icon: string,
        x: number,
        y: number,
        w: number,
        h: number,
        valueColor: string = textPrimary
    ) => {
        drawRoundedRect(x, y, w, h, 20, bgCard);

        // Icon
        drawText(icon, x + 30, y + 55, 40, textPrimary, 'left');

        // Label
        drawText(label.toUpperCase(), x + 90, y + 45, 20, textSecondary, 'left', true);

        // Main Value
        drawText(value, x + w / 2, y + 110, 56, valueColor, 'center', true);

        // Sub Value
        if (subValue) {
            drawText(subValue, x + w / 2, y + 150, 24, textSecondary, 'center');
        }
    };

    // --- Background ---
    ctx.fillStyle = bgMain;
    ctx.fillRect(0, 0, width, height);

    // --- Header ---
    const dateStr = new Date(summary.startTime).toLocaleDateString(undefined, {
        weekday: 'long',
        year: 'numeric',
        month: 'long',
        day: 'numeric',
    });
    const timeStr = new Date(summary.startTime).toLocaleTimeString(undefined, {
        hour: '2-digit',
        minute: '2-digit',
    });

    // Title
    drawText('WORKOUT SUMMARY', width / 2, 120, 40, accent, 'center', true);
    // Date
    drawText(dateStr, width / 2, 180, 28, textSecondary, 'center');
    // Time
    drawText(timeStr, width / 2, 220, 24, textSecondary, 'center');

    // --- Main Circular Stat (Duration/TSS) ---
    // Let's showcase Duration or Calories or TSS
    const centerX = width / 2;
    const centerY = 420;

    // Outer Ring
    ctx.beginPath();
    ctx.arc(centerX, centerY, 140, 0, 2 * Math.PI);
    ctx.lineWidth = 20;
    ctx.strokeStyle = '#333';
    ctx.stroke();

    // Active Ring (just visual decoration)
    ctx.beginPath();
    ctx.arc(centerX, centerY, 140, -Math.PI / 2, Math.PI, false);
    ctx.strokeStyle = accent;
    ctx.stroke();

    // Duration Text
    drawText(formatDurationLong(summary.duration), centerX, centerY + 15, 70, textPrimary, 'center', true);
    drawText('DURATION', centerX, centerY - 60, 20, textSecondary, 'center', true);

    // --- Metric Grid ---
    const gridY = 650;
    const padding = 40;
    const cardW = (width - padding * 3) / 2;
    const cardH = 200;

    // Row 1: Power & HR
    drawMetricCard(
        'Avg Power',
        `${summary.power.avg || 0}W`,
        summary.normalizedPower ? `NP ${summary.normalizedPower}W` : null,
        '',
        padding,
        gridY,
        cardW,
        cardH,
        powerColor
    );
    drawMetricCard(
        'Avg HR',
        `${summary.heartrate.avg || '--'} bpm`,
        summary.heartrate.max ? `Max ${summary.heartrate.max}` : null,
        '',
        padding * 2 + cardW,
        gridY,
        cardW,
        cardH,
        hrColor
    );

    // Row 2: VO2 & Work
    const row2Y = gridY + cardH + padding;

    // VO2Max or Cadence
    if (summary.vo2MaxEstimation) {
        drawMetricCard(
            'VO Max',
            `${summary.vo2MaxEstimation}`,
            'ml/kg/min',
            '',
            padding,
            row2Y,
            cardW,
            cardH,
            '#E91E63'
        );
    } else {
        drawMetricCard('Avg Cadence', `${summary.cadence.avg || 0} rpm`, null, '', padding, row2Y, cardW, cardH);
    }

    // Work or Calories
    // If work is available in kJ.
    const workKj = summary.work || Math.round(((summary.power.avg || 0) * (summary.duration / 1000)) / 1000);
    drawMetricCard(
        'Work',
        `${workKj} kJ`,
        summary.power.wattsPerKg ? `${summary.power.wattsPerKg} W/kg` : null,
        '',
        padding * 2 + cardW,
        row2Y,
        cardW,
        cardH
    );

    // --- Advanced Stats Strip ---
    const stripY = row2Y + cardH + padding;
    const stripH = 120;
    drawRoundedRect(padding, stripY, width - padding * 2, stripH, 20, bgCard);

    const stats = [
        { label: 'IF', value: summary.intensityFactor || '--' },
        { label: 'TSS', value: summary.trainingLoad || '--' },
        { label: 'VI', value: summary.variabilityIndex || '--' },
        { label: 'MAX PWR', value: `${summary.power.max || 0}W` },
    ];

    const sectionW = (width - padding * 2) / stats.length;
    stats.forEach((stat, i) => {
        const x = padding + i * sectionW + sectionW / 2;
        drawText(stat.label, x, stripY + 50, 24, textSecondary, 'center');
        drawText(stat.value.toString(), x, stripY + 90, 36, textPrimary, 'center', true);

        // Divider
        if (i < stats.length - 1) {
            ctx.beginPath();
            ctx.moveTo(padding + (i + 1) * sectionW, stripY + 30);
            ctx.lineTo(padding + (i + 1) * sectionW, stripY + stripH - 30);
            ctx.strokeStyle = '#333';
            ctx.lineWidth = 2;
            ctx.stroke();
        }
    });

    // --- Footer ---
    drawText('Bike Power Tracker', width / 2, height - 50, 24, '#666', 'center');

    // Convert to Blob
    return new Promise((resolve) => canvas.toBlob(resolve, 'image/png'));
}

/**
 * Trigger a download of the workout summary image
 */
export async function downloadWorkoutImage(summary: WorkoutSummary): Promise<void> {
    try {
        const blob = await generateShareImage(summary);
        if (!blob) throw new Error('Failed to generate image');

        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        const dateStr = new Date(summary.startTime).toISOString().split('T')[0];
        a.download = `workout-summary-${dateStr}.png`;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    } catch (e) {
        console.error('Error downloading workout image', e);
        alert('Could not generate image. Support might be missing in this browser.');
    }
}
