import { openDB, type IDBPDatabase } from 'idb';
import type { Goal, GoalProgress, Streak, Achievement, AchievementDefinition } from '../types/goals.js';
import { getWorkoutHistory } from '../storage/workoutStorage.js';
import { calculateWorkoutSummary } from '../calculations/summary.js'; // Fallback
import { settingsStore } from './SettingsStore.js';

const DB_NAME = 'bpt-goals';
const GOALS_STORE = 'goals';
const ACHIEVEMENTS_STORE = 'achievements';
const STREAK_STORE = 'streak';
const DB_VERSION = 2;

export class GoalsStore {
    private dbPromise: Promise<IDBPDatabase> | null = null;
    private listeners: (() => void)[] = [];

    constructor() {
        if (typeof window !== 'undefined') {
            this.init();
        }
    }

    private async init() {
        this.dbPromise = openDB(DB_NAME, DB_VERSION, {
            upgrade(db, oldVersion) {
                // Goals store
                if (!db.objectStoreNames.contains(GOALS_STORE)) {
                    db.createObjectStore(GOALS_STORE, { keyPath: 'id' });
                }

                // Achievements store (new in v2)
                if (oldVersion < 2 && !db.objectStoreNames.contains(ACHIEVEMENTS_STORE)) {
                    const achievementStore = db.createObjectStore(ACHIEVEMENTS_STORE, { keyPath: 'id' });
                    achievementStore.createIndex('type', 'type', { unique: false });
                    achievementStore.createIndex('unlockedAt', 'unlockedAt', { unique: false });
                }

                // Streak store (new in v2)
                if (oldVersion < 2 && !db.objectStoreNames.contains(STREAK_STORE)) {
                    db.createObjectStore(STREAK_STORE, { keyPath: 'id' });
                }
            },
        });
    }

    public async addGoal(goal: Omit<Goal, 'id' | 'createdAt'>): Promise<string> {
        const db = await this.dbPromise;
        if (!db) throw new Error('DB not initialized');

        const newGoal: Goal = {
            ...goal,
            id: crypto.randomUUID(),
            createdAt: Date.now(),
        };

        await db.put(GOALS_STORE, newGoal);
        this.notifyListeners();
        return newGoal.id;
    }

    public async updateGoal(goal: Goal): Promise<void> {
        const db = await this.dbPromise;
        if (!db) return;
        await db.put(GOALS_STORE, goal);
        this.notifyListeners();
    }

    public async deleteGoal(id: string): Promise<void> {
        const db = await this.dbPromise;
        if (!db) return;
        await db.delete(GOALS_STORE, id);
        this.notifyListeners();
    }

    public async getGoals(): Promise<Goal[]> {
        const db = await this.dbPromise;
        if (!db) return [];
        return db.getAll(GOALS_STORE);
    }

    // Streak Management
    public async getStreak(): Promise<Streak> {
        const db = await this.dbPromise;
        if (!db) {
            return { currentStreak: 0, longestStreak: 0, lastActivityDate: 0 };
        }

        const streak = await db.get(STREAK_STORE, 'current');
        return streak || { currentStreak: 0, longestStreak: 0, lastActivityDate: 0 };
    }

    public async updateStreak(workoutDate: number): Promise<Streak> {
        const db = await this.dbPromise;
        if (!db) throw new Error('DB not initialized');

        const streak = await this.getStreak();

        // Normalize dates to start of day for comparison
        const lastDate = streak.lastActivityDate
            ? new Date(streak.lastActivityDate).setHours(0, 0, 0, 0)
            : 0;
        const newDate = new Date(workoutDate).setHours(0, 0, 0, 0);

        if (lastDate === 0) {
            // First workout ever
            const newStreak: Streak = {
                currentStreak: 1,
                longestStreak: 1,
                lastActivityDate: workoutDate,
            };
            await db.put(STREAK_STORE, { ...newStreak, id: 'current' });
            this.notifyListeners();
            return newStreak;
        }

        const daysDiff = Math.floor((newDate - lastDate) / (1000 * 60 * 60 * 24));

        if (daysDiff === 0) {
            // Same day, no change
            return streak;
        } else if (daysDiff === 1) {
            // Consecutive day - increment streak
            const newStreak: Streak = {
                currentStreak: streak.currentStreak + 1,
                longestStreak: Math.max(streak.currentStreak + 1, streak.longestStreak),
                lastActivityDate: workoutDate,
            };
            await db.put(STREAK_STORE, { ...newStreak, id: 'current' });
            this.notifyListeners();
            return newStreak;
        } else {
            // Streak broken - reset to 1
            const newStreak: Streak = {
                currentStreak: 1,
                longestStreak: streak.longestStreak,
                lastActivityDate: workoutDate,
            };
            await db.put(STREAK_STORE, { ...newStreak, id: 'current' });
            this.notifyListeners();
            return newStreak;
        }
    }

    // Achievement Management
    public async getAchievements(): Promise<Achievement[]> {
        const db = await this.dbPromise;
        if (!db) return [];
        const all = await db.getAll(ACHIEVEMENTS_STORE);
        // Sort by unlock date, newest first
        return all.sort((a, b) => b.unlockedAt - a.unlockedAt);
    }

    private async saveAchievement(achievement: Achievement): Promise<void> {
        const db = await this.dbPromise;
        if (!db) return;
        await db.put(ACHIEVEMENTS_STORE, achievement);
        this.notifyListeners();
    }

    public async checkAndUnlockAchievements(): Promise<Achievement[]> {
        const unlocked: Achievement[] = [];
        const existing = await this.getAchievements();
        const workouts = await getWorkoutHistory();

        // Calculate totals
        let totalDistance = 0;
        let totalDuration = 0;
        let totalTSS = 0;

        for (const w of workouts) {
            const settings = settingsStore.getAll();
            const summary = w.summary || calculateWorkoutSummary(
                w.startTime,
                w.lastUpdated,
                w.measurements,
                undefined,
                settings.weight
            );

            // Distance (in km)
            if (summary.totalDistance) {
                totalDistance += summary.totalDistance / 1000;
            } else if (w.measurements?.distance && w.measurements.distance.length > 0) {
                totalDistance += w.measurements.distance[w.measurements.distance.length - 1].value / 1000;
            }

            // Duration (in hours)
            totalDuration += (w.lastUpdated - w.startTime) / 1000 / 3600;

            // TSS
            totalTSS += summary.trainingLoad || 0;
        }

        // Check distance milestones
        const distanceMilestones: AchievementDefinition[] = [
            { type: 'distance_milestone', threshold: 100, title: 'Century Rider', description: 'Rode 100 km total', icon: '' },
            { type: 'distance_milestone', threshold: 500, title: 'Distance Explorer', description: 'Rode 500 km total', icon: '' },
            { type: 'distance_milestone', threshold: 1000, title: 'Thousand Club', description: 'Rode 1000 km total', icon: '' },
            { type: 'distance_milestone', threshold: 5000, title: 'Distance Warrior', description: 'Rode 5000 km total', icon: '' },
            { type: 'distance_milestone', threshold: 10000, title: 'Ultra Cyclist', description: 'Rode 10,000 km total', icon: '' },
        ];

        for (const def of distanceMilestones) {
            if (totalDistance >= def.threshold && !existing.some(a => a.title === def.title)) {
                const achievement: Achievement = {
                    id: crypto.randomUUID(),
                    type: def.type,
                    title: def.title,
                    description: def.description,
                    unlockedAt: Date.now(),
                    icon: def.icon,
                };
                await this.saveAchievement(achievement);
                unlocked.push(achievement);
            }
        }

        // Check time milestones
        const timeMilestones: AchievementDefinition[] = [
            { type: 'time_milestone', threshold: 10, title: 'Time Starter', description: 'Rode 10 hours total', icon: '' },
            { type: 'time_milestone', threshold: 50, title: 'Time Committed', description: 'Rode 50 hours total', icon: '' },
            { type: 'time_milestone', threshold: 100, title: 'Century Hours', description: 'Rode 100 hours total', icon: '' },
            { type: 'time_milestone', threshold: 500, title: 'Time Champion', description: 'Rode 500 hours total', icon: '' },
        ];

        for (const def of timeMilestones) {
            if (totalDuration >= def.threshold && !existing.some(a => a.title === def.title)) {
                const achievement: Achievement = {
                    id: crypto.randomUUID(),
                    type: def.type,
                    title: def.title,
                    description: def.description,
                    unlockedAt: Date.now(),
                    icon: def.icon,
                };
                await this.saveAchievement(achievement);
                unlocked.push(achievement);
            }
        }

        // Check TSS milestones
        const tssMilestones: AchievementDefinition[] = [
            { type: 'tss_milestone', threshold: 500, title: 'TSS Novice', description: 'Accumulated 500 TSS', icon: '' },
            { type: 'tss_milestone', threshold: 2000, title: 'TSS Builder', description: 'Accumulated 2000 TSS', icon: '' },
            { type: 'tss_milestone', threshold: 5000, title: 'TSS Master', description: 'Accumulated 5000 TSS', icon: '' },
        ];

        for (const def of tssMilestones) {
            if (totalTSS >= def.threshold && !existing.some(a => a.title === def.title)) {
                const achievement: Achievement = {
                    id: crypto.randomUUID(),
                    type: def.type,
                    title: def.title,
                    description: def.description,
                    unlockedAt: Date.now(),
                    icon: def.icon,
                };
                await this.saveAchievement(achievement);
                unlocked.push(achievement);
            }
        }

        // Check streak milestones
        const streak = await this.getStreak();
        const streakMilestones: AchievementDefinition[] = [
            { type: 'streak_milestone', threshold: 3, title: 'Getting Consistent', description: '3 day streak', icon: '' },
            { type: 'streak_milestone', threshold: 7, title: 'Week Warrior', description: '7 day streak', icon: '' },
            { type: 'streak_milestone', threshold: 14, title: 'Two Week Titan', description: '14 day streak', icon: '' },
            { type: 'streak_milestone', threshold: 30, title: 'Monthly Master', description: '30 day streak', icon: '' },
            { type: 'streak_milestone', threshold: 100, title: 'Century Streak', description: '100 day streak', icon: '' },
        ];

        for (const def of streakMilestones) {
            if (streak.currentStreak >= def.threshold && !existing.some(a => a.title === def.title)) {
                const achievement: Achievement = {
                    id: crypto.randomUUID(),
                    type: def.type,
                    title: def.title,
                    description: def.description,
                    unlockedAt: Date.now(),
                    icon: def.icon,
                };
                await this.saveAchievement(achievement);
                unlocked.push(achievement);
            }
        }

        // Check goal completions
        const goals = await this.getGoals();
        for (const goal of goals) {
            const progress = await this.getProgress(goal);
            if (progress.isCompleted && !existing.some(a => a.goalId === goal.id)) {
                const achievement: Achievement = {
                    id: crypto.randomUUID(),
                    type: 'goal_completed',
                    title: `Goal: ${goal.title}`,
                    description: `Achieved ${progress.formattedTarget} in ${progress.periodLabel}`,
                    unlockedAt: Date.now(),
                    goalId: goal.id,
                    icon: '',
                };
                await this.saveAchievement(achievement);
                unlocked.push(achievement);
            }
        }

        return unlocked;
    }

    public async getProgress(goal: Goal): Promise<GoalProgress> {
        const workouts = await getWorkoutHistory(); // This gets all.

        // Define period boundaries
        const now = new Date();
        let start: Date;
        let end: Date;
        let periodLabel = '';

        if (goal.period === 'weekly') {
            // Start of week (Monday)
            const day = now.getDay();
            const diff = now.getDate() - day + (day === 0 ? -6 : 1); // adjust when day is sunday
            start = new Date(now.setDate(diff));
            start.setHours(0, 0, 0, 0);

            end = new Date(start);
            end.setDate(start.getDate() + 7);
            periodLabel = 'This Week';
        } else {
            // Monthly
            start = new Date(now.getFullYear(), now.getMonth(), 1);
            end = new Date(now.getFullYear(), now.getMonth() + 1, 0);
            end.setHours(23, 59, 59, 999);
            periodLabel = start.toLocaleString('default', { month: 'long', year: 'numeric' });
        }

        const filteredWorkouts = workouts.filter((w) => {
            return w.startTime >= start.getTime() && w.startTime <= end.getTime();
        });

        let current = 0;

        for (const w of filteredWorkouts) {
            let val = 0;
            const settings = settingsStore.getAll();
            const summary =
                w.summary ||
                calculateWorkoutSummary(w.startTime, w.lastUpdated, w.measurements, undefined, settings.weight);

            switch (goal.type) {
                case 'distance':
                    // distance in meters in DB? summary.totalDistance usually?
                    // Let's check summary structure again.
                    // StoredWorkout -> summary (WorkoutSummary) -> doesn't have totalDistance explicit in interface sometimes?
                    // measurements.distance is array.
                    // Let's assume summary has something or we calc.
                    // Re-checking calculateWorkoutSummary return type:
                    // It returns: duration, power, heartrate, cadence, etc.
                    // It does NOT explicitly return totalDistance in the interface I read earlier!
                    // Wait, `calculateWorkoutSummary` in `summary.ts` uses `measurements` input.
                    // If `measurements.distance` exists, we can sum it or get last value?
                    // `distance` is usually cumulative.

                    if (summary.totalDistance) {
                        val = summary.totalDistance;
                    } else if (w.measurements?.distance && w.measurements.distance.length > 0) {
                        // Last point value is total distance
                        val = w.measurements.distance[w.measurements.distance.length - 1].value;
                    }
                    // Val is in meters. Target usually km.
                    val = val / 1000;
                    break;
                case 'duration':
                    // duration in ms. Target usually hours.
                    val = (w.lastUpdated - w.startTime) / 1000 / 3600;
                    break;
                case 'tss':
                    val = summary.trainingLoad || 0;
                    break;
                case 'count':
                    val = 1;
                    break;
            }
            current += val;
        }

        // Formatting
        let formattedCurrent = '';
        let formattedTarget = '';

        if (goal.type === 'distance') {
            formattedCurrent = `${current.toFixed(1)} km`;
            formattedTarget = `${goal.target} km`;
        } else if (goal.type === 'duration') {
            formattedCurrent = `${current.toFixed(1)} hrs`;
            formattedTarget = `${goal.target} hrs`;
        } else if (goal.type === 'tss') {
            formattedCurrent = Math.round(current).toString();
            formattedTarget = goal.target.toString();
        } else {
            formattedCurrent = Math.round(current).toString();
            formattedTarget = goal.target.toString();
        }

        // Calculate "on track" status
        const nowTime = Date.now();
        let onTrack = false;

        if (nowTime > end.getTime()) {
            // Period ended - just check if completed
            onTrack = current >= goal.target;
        } else {
            // Period is active - check if pace is sufficient
            const totalDuration = end.getTime() - start.getTime();
            const elapsed = nowTime - start.getTime();
            const expectedProgress = (elapsed / totalDuration) * goal.target;
            // Consider "on track" if at least 90% of expected progress
            onTrack = current >= expectedProgress * 0.9;
        }

        return {
            goalId: goal.id,
            current,
            target: goal.target,
            percent: Math.min(100, (current / goal.target) * 100),
            formattedCurrent,
            formattedTarget,
            periodLabel,
            remaining: Math.max(0, goal.target - current),
            isCompleted: current >= goal.target,
            onTrack,
            periodStart: start.getTime(),
            periodEnd: end.getTime(),
        };
    }

    public subscribe(listener: () => void): () => void {
        this.listeners.push(listener);
        return () => {
            this.listeners = this.listeners.filter((l) => l !== listener);
        };
    }

    private notifyListeners() {
        this.listeners.forEach((l) => l());
    }
}

export const goalsStore = new GoalsStore();
