/**
 * Zone State Management
 *
 * Tracks time spent in each power and heart rate zone during a workout.
 * Integrates with SettingsStore for zone thresholds.
 *
 * @module ZoneState
 */

import { settingsStore } from './state/SettingsStore.js';
import type { ZoneDefinition } from './types/zones.js';

/**
 * Zone information with time tracking
 */
export interface ZoneInfo extends ZoneDefinition {
    zone: number;
    timeInZoneMs: number;
}

/**
 * Current zone status for real-time display
 */
export interface CurrentZoneStatus {
    zone: number;
    name: string;
    percentInZone: number; // 0-100, position within zone range
    color: string;
}

/**
 * Zone distribution data for summary
 */
export interface ZoneDistribution {
    zones: ZoneInfo[];
    totalTimeMs: number;
}

/**
 * State change callback type
 */
export type ZoneStateChangeCallback = () => void;

/**
 * Zone State class for tracking time in zones
 */
export class ZoneState {
    private _powerZones: ZoneInfo[] = [];
    private _hrZones: ZoneInfo[] = [];
    private _lastPowerZone: number | null = null;
    private _lastHrZone: number | null = null;
    private _lastUpdateTime: number | null = null;
    private _ftp: number = 200;
    private _maxHr: number = 190;
    private _currentPowerZone: CurrentZoneStatus | null = null;
    private _currentHrZone: CurrentZoneStatus | null = null;
    private _onChangeCallbacks: ZoneStateChangeCallback[] = [];
    private _unsubscribeSettings: (() => void) | null = null;

    constructor() {
        this.init();
    }

    private init() {
        // Initial load
        this.updateFromSettings();

        // Subscribe to changes
        this._unsubscribeSettings = settingsStore.onChange(() => {
            this.updateFromSettings();
        });
    }

    /**
     * Update zones from settings store
     * Note: This resets accumulation if zones change structure
     */
    public updateFromSettings(): void {
        const settings = settingsStore.getAll();

        // Check if FTP or MaxHR changed significantly or if zone definitions changed
        // For simplicity, we re-calculate absolute zones.
        // We TRY to preserve timeInZone if the number of zones matches, but strictly speaking
        // changing FTP changes the bin meaning, so resetting is theoretically more correct for a "new" distribution.
        // However, users might adjust FTP slightly and not want to lose data.
        // Let's preserve times if array length matches for now, ensuring smooth UX.

        this._ftp = settings.ftp;
        this._maxHr = settings.maxHeartrate;

        const newPowerZones = settings.powerZones.map((z, i) => ({
            ...z,
            zone: i + 1,
            // Convert percentage to absolute watts
            min: Math.round((z.min * this._ftp) / 100),
            max: z.max >= 1000 ? 9999 : Math.round((z.max * this._ftp) / 100), // Handle open-ended
            timeInZoneMs:
                this._powerZones[i] && this._powerZones[i].name === z.name ? this._powerZones[i].timeInZoneMs : 0,
        }));

        const newHrZones = settings.heartrateZones.map((z, i) => ({
            ...z,
            zone: i + 1,
            // Convert percentage to absolute bpm
            min: Math.round((z.min * this._maxHr) / 100),
            max:
                z.max >= 1000 || i === settings.heartrateZones.length - 1
                    ? this._maxHr
                    : Math.round((z.max * this._maxHr) / 100),
            // Note: settings.heartrateZones[last].max usually is 100% or close.
            // We clamp max of last zone to maxHr if it's close to 100%
            timeInZoneMs: this._hrZones[i] && this._hrZones[i].name === z.name ? this._hrZones[i].timeInZoneMs : 0,
        }));

        // Ensure last HR zone max is MaxHR just to be safe if definition is exactly 100%
        if (newHrZones.length > 0) {
            newHrZones[newHrZones.length - 1].max = Math.max(newHrZones[newHrZones.length - 1].max, this._maxHr);
        }

        this._powerZones = newPowerZones;
        this._hrZones = newHrZones;

        this._notifyChange();
    }

    /**
     * Load profile and update zones (Compatibility alias)
     */
    loadProfile(): void {
        this.updateFromSettings();
    }

    /**
     * Register a callback for state changes
     */
    onChange(callback: ZoneStateChangeCallback): void {
        this._onChangeCallbacks.push(callback);
    }

    /**
     * Remove a state change callback
     */
    offChange(callback: ZoneStateChangeCallback): void {
        const index = this._onChangeCallbacks.indexOf(callback);
        if (index > -1) {
            this._onChangeCallbacks.splice(index, 1);
        }
    }

    /**
     * Notify listeners of state change
     */
    private _notifyChange(): void {
        for (const callback of this._onChangeCallbacks) {
            try {
                callback();
            } catch (e) {
                console.error('Zone state change callback error:', e);
            }
        }
    }

    /**
     * Update power value and track zone time
     */
    updatePower(power: number, timestamp: number = Date.now()): CurrentZoneStatus | null {
        if (!this._powerZones.length) return null;

        // Find zone
        let zoneIndex = -1;
        for (let i = 0; i < this._powerZones.length; i++) {
            if (power >= this._powerZones[i].min && power < this._powerZones[i].max) {
                zoneIndex = i;
                break;
            }
        }

        // Handle out of bounds (above max)
        if (zoneIndex === -1 && power >= this._powerZones[this._powerZones.length - 1].max) {
            zoneIndex = this._powerZones.length - 1;
        }

        if (zoneIndex === -1) return null;

        const currentZone = zoneIndex + 1;

        // Track time in zone
        if (this._lastUpdateTime !== null && this._lastPowerZone !== null) {
            const timeDelta = timestamp - this._lastUpdateTime;
            const zoneData = this._powerZones[this._lastPowerZone - 1];
            if (zoneData && timeDelta > 0 && timeDelta < 5000) {
                // Only count if less than 5 seconds (avoid large gaps)
                zoneData.timeInZoneMs += timeDelta;
            }
        }

        this._lastPowerZone = currentZone;
        this._lastUpdateTime = timestamp;

        // Calculate position within zone
        const zoneData = this._powerZones[zoneIndex];
        const range = zoneData.max - zoneData.min;
        const percentInZone = range > 0 ? Math.min(100, Math.max(0, ((power - zoneData.min) / range) * 100)) : 50;

        this._currentPowerZone = {
            zone: currentZone,
            name: zoneData.name,
            percentInZone,
            color: zoneData.color,
        };

        this._notifyChange();
        return this._currentPowerZone;
    }

    /**
     * Update heart rate value and track zone time
     */
    updateHeartRate(hr: number, timestamp: number = Date.now()): CurrentZoneStatus | null {
        if (!this._hrZones.length) return null;

        let zoneIndex = -1;
        for (let i = 0; i < this._hrZones.length; i++) {
            if (hr >= this._hrZones[i].min && hr <= this._hrZones[i].max) {
                zoneIndex = i;
                break;
            }
        }

        // Handle out of bounds
        if (zoneIndex === -1 && hr > this._hrZones[this._hrZones.length - 1].max) {
            zoneIndex = this._hrZones.length - 1;
        }

        if (zoneIndex === -1) return null;

        const currentZone = zoneIndex + 1;

        // Track time in zone
        if (this._lastUpdateTime !== null && this._lastHrZone !== null) {
            const timeDelta = timestamp - this._lastUpdateTime;
            const zoneData = this._hrZones[this._lastHrZone - 1];
            if (zoneData && timeDelta > 0 && timeDelta < 5000) {
                zoneData.timeInZoneMs += timeDelta;
            }
        }

        this._lastHrZone = currentZone;
        // Only update if no power update occurred (prevent double counting)
        if (this._lastUpdateTime === null || timestamp - this._lastUpdateTime > 50) {
            this._lastUpdateTime = timestamp;
        }

        // Calculate position within zone
        const zoneData = this._hrZones[zoneIndex];
        const range = zoneData.max - zoneData.min;
        const percentInZone = range > 0 ? Math.min(100, Math.max(0, ((hr - zoneData.min) / range) * 100)) : 50;

        this._currentHrZone = {
            zone: currentZone,
            name: zoneData.name,
            percentInZone,
            color: zoneData.color,
        };

        this._notifyChange();
        return this._currentHrZone;
    }

    /**
     * Get current power zone status
     */
    getCurrentPowerZone(): CurrentZoneStatus | null {
        return this._currentPowerZone;
    }

    /**
     * Get current HR zone status
     */
    getCurrentHrZone(): CurrentZoneStatus | null {
        return this._currentHrZone;
    }

    /**
     * Get power zone distribution for summary
     */
    getPowerZoneDistribution(): ZoneDistribution {
        const totalTimeMs = this._powerZones.reduce((sum, z) => sum + z.timeInZoneMs, 0);
        return {
            zones: [...this._powerZones],
            totalTimeMs,
        };
    }

    /**
     * Get HR zone distribution for summary
     */
    getHrZoneDistribution(): ZoneDistribution {
        const totalTimeMs = this._hrZones.reduce((sum, z) => sum + z.timeInZoneMs, 0);
        return {
            zones: [...this._hrZones],
            totalTimeMs,
        };
    }

    /**
     * Check if power zones are configured
     */
    hasPowerZones(): boolean {
        return this._ftp !== null && this._ftp > 0;
    }

    /**
     * Check if HR zones are configured
     */
    hasHrZones(): boolean {
        return this._maxHr !== null && this._maxHr > 0;
    }

    /**
     * Get FTP value
     */
    getFtp(): number | null {
        return this._ftp;
    }

    /**
     * Get Max HR value
     */
    getMaxHr(): number | null {
        return this._maxHr;
    }

    /**
     * Reset all zone tracking data
     */
    reset(): void {
        this._powerZones.forEach((z) => (z.timeInZoneMs = 0));
        this._hrZones.forEach((z) => (z.timeInZoneMs = 0));
        this._lastPowerZone = null;
        this._lastHrZone = null;
        this._lastUpdateTime = null;
        this._currentPowerZone = null;
        this._currentHrZone = null;
        this._notifyChange();
    }

    /**
     * Dispose resources
     */
    dispose(): void {
        if (this._unsubscribeSettings) {
            this._unsubscribeSettings();
            this._unsubscribeSettings = null;
        }
    }

    /**
     * Export zone data as JSON (for exports/summary)
     */
    toJSON(): {
        powerZones: ZoneInfo[];
        hrZones: ZoneInfo[];
        ftp: number | null;
        maxHr: number | null;
    } {
        return {
            powerZones: [...this._powerZones],
            hrZones: [...this._hrZones],
            ftp: this._ftp,
            maxHr: this._maxHr,
        };
    }
}
