/**
 * Connection Button Initialization
 *
 * Sets up Bluetooth sensor connection buttons with connect/disconnect functionality.
 *
 * @module initConnectionButtons
 */

import { connectGps } from './connect-gps.js';
import { BluetoothFactory } from './services/bluetooth/factory.js';
import { elements } from './elements.js';
import type { MeasurementsState } from './measurements-state.js';
import type { Measurement, TreadmillMeasurement } from './types/measurements.js';
import type { ConnectionsState } from './getInitState.js';
import type { SensorType } from './types/connections.js';
import type { ConnectionStatus } from './types/bluetooth.js';
import { showNotification } from './ui/notifications.js';
import { showConnectionError, showReconnectionFailed } from './ui/connectionError.js';
import { connectionStorage } from './storage/connectionStorage.js';

export interface ConnectionManager {
    reconnectStored: () => Promise<void>;
}

/**
 * Parameters for connection button initialization
 */
interface InitConnectionButtonsParams {
    connectionsState: ConnectionsState;
    measurementsState: MeasurementsState;
}

/**
 * Emoji icons for each sensor type
 */
const emojis: Record<SensorType, string> = {
    power: '',
    heartrate: '',
    cadence: '',
    speed: '',
    distance: '',
    altitude: '',
    gps: '',
    treadmill: '',
    treadmillSpeed: '',
    indoorBike: '',
    rower: '',
    crossTrainer: '',
    stepClimber: '',
    weightScale: '',
    rsc: '',
    concept2: '',
    skiErg: '',
};

/**
 * Get formatted sensor type label
 */
const getSensorLabel = (key: SensorType): string => {
    return (
        key.charAt(0).toUpperCase() +
        key
            .replace(/([A-Z])/g, ' $1')
            .trim()
            .slice(1)
    );
};

/**
 * Initialize connection buttons for all sensor types.
 *
 * Sets up click handlers for connect/disconnect buttons that:
 * - Connect to Bluetooth sensors
 * - Update connection state
 * - Register measurement listeners
 * - Update UI to reflect connection status
 * - Display device name when connected
 * - Handle auto-reconnect status updates
 *
 * @param params - Object containing state objects
 */
/** Timeout duration before showing "taking too long" indicator (ms) */
const CONNECTION_TIMEOUT_MS = 10000;

/** Map to track connection timeout timers */
const connectionTimeouts: Map<SensorType, ReturnType<typeof setTimeout>> = new Map();

export const initConnectionButtons = ({
    connectionsState,
    measurementsState,
}: InitConnectionButtonsParams): ConnectionManager => {
    // Initialize Modal Logic
    const connectSensorBtn = document.getElementById('connectSensorBtn');
    const sensorModal = document.getElementById('sensorModal') as HTMLDialogElement | null;
    const closeSensorModalBtn = document.getElementById('closeSensorModal');

    if (connectSensorBtn && sensorModal) {
        connectSensorBtn.addEventListener('click', () => {
            if (typeof sensorModal.showModal === 'function') {
                sensorModal.showModal();
            } else {
                sensorModal.setAttribute('open', '');
            }
        });
    }

    if (closeSensorModalBtn && sensorModal) {
        closeSensorModalBtn.addEventListener('click', () => {
            if (typeof sensorModal.close === 'function') {
                sensorModal.close();
            } else {
                sensorModal.removeAttribute('open');
            }
        });
    }

    if (sensorModal) {
        sensorModal.addEventListener('click', (event) => {
            if (event.target === sensorModal) {
                if (typeof sensorModal.close === 'function') {
                    sensorModal.close();
                } else {
                    sensorModal.removeAttribute('open');
                }
            }
        });
    }

    /**
     * Update the connected count badge in the modal header
     */
    const updateConnectedCount = (): void => {
        const countElem = document.getElementById('connectedCount');
        const disconnectAllBtn = document.getElementById('disconnectAllBtn');

        const connectedTypes = supportedTypes.filter((type) => connectionsState[type]?.isConnected);
        const count = connectedTypes.length;

        if (countElem) {
            countElem.textContent = count === 0 ? '0 connected' : count === 1 ? '1 connected' : `${count} connected`;

            if (count > 0) {
                countElem.classList.add('has-connections');
            } else {
                countElem.classList.remove('has-connections');
            }
        }

        // Show/hide Disconnect All button based on connection count
        if (disconnectAllBtn) {
            if (count > 0) {
                disconnectAllBtn.classList.add('visible');
            } else {
                disconnectAllBtn.classList.remove('visible');
            }
        }
    };

    /**
     * Clear connection timeout for a sensor
     */
    const clearConnectionTimeout = (key: SensorType): void => {
        const existingTimeout = connectionTimeouts.get(key);
        if (existingTimeout) {
            clearTimeout(existingTimeout);
            connectionTimeouts.delete(key);
        }
    };

    /**
     * Start connection timeout timer for a sensor
     */
    const startConnectionTimeout = (key: SensorType): void => {
        clearConnectionTimeout(key);

        const timeoutId = setTimeout(() => {
            const connectElem = elements[key]?.connect;
            if (connectElem?.classList.contains('connecting')) {
                connectElem.classList.add('timeout');
            }
        }, CONNECTION_TIMEOUT_MS);

        connectionTimeouts.set(key, timeoutId);
    };

    /**
     * Update button visual state based on connection status
     */
    const updateButtonText = (
        key: SensorType,
        status: 'connected' | 'disconnected' | 'reconnecting' | 'connecting',
        deviceName?: string
    ): void => {
        const connectElem = elements[key]?.connect;
        if (!connectElem) return;

        const statusSpan = connectElem.querySelector('.sensor-status');
        const deviceNameSpan = connectElem.querySelector('.sensor-device-name');

        // Remove all state classes first
        connectElem.classList.remove('connected', 'connecting', 'timeout');
        connectElem.style.borderColor = '';

        // Clear any existing timeout when status changes
        if (status !== 'connecting' && status !== 'reconnecting') {
            clearConnectionTimeout(key);
        }

        if (statusSpan) {
            switch (status) {
                case 'connected':
                    statusSpan.textContent = 'Connected';
                    connectElem.classList.add('connected');
                    if (deviceNameSpan && deviceName) {
                        deviceNameSpan.textContent = deviceName;
                    }
                    break;
                case 'connecting':
                case 'reconnecting':
                    statusSpan.textContent = status === 'connecting' ? 'Connecting...' : 'Reconnecting...';
                    connectElem.classList.add('connecting');
                    // Start timeout timer
                    startConnectionTimeout(key);
                    break;
                case 'disconnected': {
                    statusSpan.textContent = 'Disconnected';
                    // Show last connected device name if available
                    const lastDeviceName = connectionStorage.getDeviceName(key);
                    if (deviceNameSpan) {
                        deviceNameSpan.textContent = lastDeviceName || '';
                    }
                    break;
                }
            }
        } else {
            // Fallback for old buttons if any exist
            const emoji = emojis[key];
            const label = getSensorLabel(key);
            switch (status) {
                case 'connected':
                    if (deviceName) {
                        connectElem.textContent = `${emoji} Disconnect ${label} (${deviceName})`;
                    } else {
                        connectElem.textContent = `${emoji} Disconnect ${label}`;
                    }
                    break;
                case 'connecting':
                case 'reconnecting':
                    connectElem.textContent = `${emoji} ${status === 'connecting' ? 'Connecting' : 'Reconnecting'} ${label}...`;
                    break;
                case 'disconnected':
                    connectElem.textContent = `${emoji} Connect ${label}`;
                    break;
            }
        }

        // Update connected count after state change
        updateConnectedCount();
    };

    /**
     * Disconnect from a sensor
     */
    const disconnectFn = (key: SensorType): void => {
        const connectionState = connectionsState[key];
        if (!connectionState) return;

        if (typeof connectionState.disconnect === 'function') {
            connectionState.disconnect();
            connectionState.disconnect = null;
            connectionState.isConnected = false;

            updateButtonText(key, 'disconnected');

            const displayElem = elements[key]?.display;
            if (displayElem) {
                displayElem.textContent = '--';
            }
        }
    };

    /**
     * Disconnect all connected sensors
     */
    const disconnectAll = (): void => {
        const connectedTypes = supportedTypes.filter((type) => connectionsState[type]?.isConnected);

        if (connectedTypes.length === 0) return;

        connectedTypes.forEach((type) => {
            disconnectFn(type);
        });

        showNotification(`Disconnected ${connectedTypes.length} sensor${connectedTypes.length > 1 ? 's' : ''}`, 'info');
    };

    // Set up Disconnect All button handler
    const disconnectAllBtn = document.getElementById('disconnectAllBtn');
    if (disconnectAllBtn) {
        disconnectAllBtn.addEventListener('click', disconnectAll);
    }

    /**
     * Handle connection status changes from auto-reconnect
     */
    const handleStatusChange =
        (key: SensorType, deviceName: string) =>
        (status: ConnectionStatus): void => {
            const label = getSensorLabel(key);

            switch (status) {
                case 'connected':
                    updateButtonText(key, 'connected', deviceName);
                    showNotification(`${label} sensor reconnected`, 'success');
                    break;
                case 'reconnecting':
                    updateButtonText(key, 'reconnecting');
                    break;
                case 'failed':
                    if (connectionsState[key]) {
                        connectionsState[key]!.isConnected = false;
                        connectionsState[key]!.disconnect = null;
                    }
                    updateButtonText(key, 'disconnected');
                    // Show reconnection failed dialog with retry option
                    showReconnectionFailed(key, () => connectSensor(key));
                    break;
                case 'disconnected':
                    // Initial disconnect event, wait for reconnect attempt
                    break;
            }
        };

    /**
     * Connect to a sensor
     */
    const connectSensor = async (key: SensorType, deviceId?: string): Promise<void> => {
        try {
            updateButtonText(key, 'connecting'); // Show connecting state

            let connection: any;

            if (key === 'gps') {
                // GPS has a different interface
                const gpsConnection = await connectGps((point) => {
                    measurementsState.addGps(point);
                });

                // Manually adapt
                const state = connectionsState[key];
                if (state) {
                    state.disconnect = async () => {
                        await gpsConnection.stop();
                    };
                    state.isConnected = true;
                }
                updateButtonText(key, 'connected', gpsConnection.deviceName);
                showNotification(`Connected to ${getSensorLabel(key)}`, 'success');
                connectionStorage.saveId('gps', 'enabled');
                if (gpsConnection.deviceName) {
                    connectionStorage.saveDeviceName('gps', gpsConnection.deviceName);
                }
                return;
            }

            // General Factory Connection
            switch (key) {
                case 'power':
                    connection = await BluetoothFactory.connectPower(deviceId);
                    connection.addListener((entry: Measurement) => measurementsState.addPower(entry));
                    break;
                case 'heartrate':
                    connection = await BluetoothFactory.connectHeartRate(deviceId);
                    connection.addListener((entry: Measurement) => measurementsState.addHeartrate(entry));
                    break;
                case 'cadence':
                    connection = await BluetoothFactory.connectCadence(deviceId);
                    connection.addListener((entry: Measurement) => measurementsState.addCadence(entry));
                    break;
                case 'speed':
                    connection = await BluetoothFactory.connectSpeed();
                    // Connect speed listener
                    connection.addSpeedListener((entry: Measurement) => measurementsState.addSpeed(entry));
                    // Also connect cadence listener if available (CSCS supports both)
                    connection.addCadenceListener((entry: Measurement) => measurementsState.addCadence(entry));
                    break;
                case 'treadmill':
                    connection = await BluetoothFactory.connectTreadmill(deviceId);
                    connection.addListener((entry: TreadmillMeasurement) => measurementsState.addTreadmillData(entry));
                    break;
                case 'indoorBike':
                    connection = await BluetoothFactory.connectIndoorBike();
                    connection.addListener((entry: any) => {
                        if (entry.power !== null)
                            measurementsState.addPower({ timestamp: entry.timestamp, value: entry.power });
                        if (entry.cadence !== null)
                            measurementsState.addCadence({ timestamp: entry.timestamp, value: entry.cadence });
                    });
                    break;
                case 'rower':
                    connection = await BluetoothFactory.connectRowing(); // or connectRower depending on factory name
                    connection.addListener((entry: any) => {
                        if (entry.power !== null)
                            measurementsState.addPower({ timestamp: entry.timestamp, value: entry.power });
                        // Rower stroke rate -> cadence?
                        if (entry.strokeRate !== null)
                            measurementsState.addCadence({ timestamp: entry.timestamp, value: entry.strokeRate });
                    });
                    break;
                case 'crossTrainer':
                    connection = await BluetoothFactory.connectCrossTrainer();
                    connection.addListener((entry: any) => {
                        if (entry.power !== null)
                            measurementsState.addPower({ timestamp: entry.timestamp, value: entry.power });
                        if (entry.stepRate !== null)
                            measurementsState.addCadence({ timestamp: entry.timestamp, value: entry.stepRate });
                    });
                    break;
                case 'stepClimber':
                    connection = await BluetoothFactory.connectStepClimber();
                    connection.addListener((entry: any) => {
                        if (entry.power !== null)
                            measurementsState.addPower({ timestamp: entry.timestamp, value: entry.power });
                        if (entry.stepRate !== null)
                            measurementsState.addCadence({ timestamp: entry.timestamp, value: entry.stepRate });
                    });
                    break;
                case 'rsc':
                    connection = await BluetoothFactory.connectRunningSensor();
                    connection.addListener((entry: any) => {
                        if (entry.cadence !== null)
                            measurementsState.addCadence({ timestamp: entry.timestamp, value: entry.cadence });
                        if (entry.speed !== null)
                            measurementsState.addSpeed({ timestamp: entry.timestamp, value: entry.speed });
                    });
                    break;
                case 'weightScale':
                    connection = await BluetoothFactory.connectWeightScale();
                    connection.addListener((entry: any) => {
                        // TODO: Support weight in measurements state
                        console.log('Weight measurement:', entry);
                        showNotification(`Weight: ${entry.weight} kg`, 'success');
                    });
                    break;
                default:
                    throw new Error(`Unsupported sensor type: ${key}`);
            }

            const { disconnect, deviceName, onStatusChange } = connection;

            // Update state
            const state = connectionsState[key];
            if (state) {
                state.disconnect = disconnect;
                state.isConnected = true;
            }

            // Update UI
            updateButtonText(key, 'connected', deviceName);
            showNotification(`Connected to ${getSensorLabel(key)}`, 'success');

            // Save device name for display when disconnected
            if (deviceName) {
                connectionStorage.saveDeviceName(key, deviceName);
            }

            // Subscribe to status changes for auto-reconnect feedback
            if (onStatusChange) {
                onStatusChange(handleStatusChange(key, deviceName || 'Sensor'));
            }
        } catch (error) {
            updateButtonText(key, 'disconnected');

            // Check if user wants to retry
            const shouldRetry = await showConnectionError(error, key, () => connectSensor(key));
            if (!shouldRetry) {
                // Reset state just in case
                if (connectionsState[key]) {
                    connectionsState[key]!.isConnected = false;
                }
            }
        }
    };

    /**
     * Add click listener to a button
     */
    const addListener = (key: SensorType): void => {
        const connectElem = elements[key]?.connect;
        if (!connectElem) return;

        const handler = (e: Event) => {
            // Prevent propagation
            e.stopPropagation();

            const isConnected = connectionsState[key]?.isConnected;
            if (isConnected) {
                disconnectFn(key);
            } else {
                connectSensor(key);
            }
        };

        // Add click listener
        connectElem.addEventListener('click', handler);
    };

    // Initialize all supported buttons
    const supportedTypes: SensorType[] = [
        'power',
        'heartrate',
        'cadence',
        'speed',
        'gps',
        'treadmill',
        'indoorBike',
        'rower',
        'crossTrainer',
        'stepClimber',
        'weightScale',
        'rsc',
        'concept2',
        'skiErg',
    ];
    supportedTypes.forEach(addListener);

    const reconnectStored = async (): Promise<void> => {
        const ids = connectionStorage.getIds();
        const promises: Promise<void>[] = [];

        const queueConnect = (type: SensorType) => {
            if (!ids[type] && ids[type] !== 'enabled') return;

            // Filter supported types
            if (!supportedTypes.includes(type)) return;

            const id = ids[type];
            if (type === 'gps' && id === 'enabled') {
                promises.push(connectSensor('gps'));
            } else if (id) {
                promises.push(connectSensor(type, id));
            }
        };

        supportedTypes.forEach(queueConnect);
        await Promise.allSettled(promises);
    };

    /**
     * Initialize last device names display for disconnected sensors
     */
    const initLastDeviceNames = (): void => {
        supportedTypes.forEach((type) => {
            const connectElem = elements[type]?.connect;
            if (!connectElem) return;

            const deviceNameSpan = connectElem.querySelector('.sensor-device-name');
            if (deviceNameSpan) {
                const lastDeviceName = connectionStorage.getDeviceName(type);
                if (lastDeviceName) {
                    deviceNameSpan.textContent = lastDeviceName;
                }
            }
        });
    };

    // Initialize last device names display on page load
    initLastDeviceNames();

    return { reconnectStored };
};
