/**
 * Scheduled Workout Storage
 *
 * Manages retrieval of planned workouts from active training plans
 * and manual schedules.
 */

import { planRepository } from '../plans/planRepository.js';
import type { TrainingPlan } from '../plans/types.js';
import { getWorkoutById, getWorkoutDuration } from '../workouts/workoutLibrary.js';
import { getCustomWorkouts } from '../storage/customWorkouts.js';

export interface CalendarEvent {
    id: string;
    title: string;
    date: Date;
    type: 'plan' | 'manual';
    status: 'planned' | 'completed' | 'skipped';
    duration?: number;
    description?: string;
    planId?: string;
    week?: number;
    day?: number;
}

/**
 * Get all scheduled events within a date range
 */
export function getScheduledEvents(startDate: Date, endDate: Date): CalendarEvent[] {
    const events: CalendarEvent[] = [];

    // 1. Get Plan Events
    const planProgress = planRepository.getActiveProgress();
    if (planProgress) {
        const plan = planRepository.getPlan(planProgress.planId);
        if (plan) {
            events.push(...getPlanEvents(plan, planProgress.startDate, startDate, endDate));
        }
    }

    // 2. Get Manual Events (TODO: Implement storage for manual events)
    // const manualEvents = getManualEvents();
    // events.push(...manualEvents);

    return events.sort((a, b) => a.date.getTime() - b.date.getTime());
}

/**
 * Project a plan onto a date range
 */
function getPlanEvents(plan: TrainingPlan, planStartDate: number, rangeStart: Date, rangeEnd: Date): CalendarEvent[] {
    const events: CalendarEvent[] = [];
    const planStart = new Date(planStartDate);

    // Normalize plan start to the Monday of that week?
    // Or assume planStart IS the start.
    // Let's assume planStart is Day 1 of Week 1.
    // If the plan defines specific days of week (e.g. 0=Sun), we should align.

    // Strategy: Iterate through weeks of the plan.
    plan.weeks.forEach((week) => {
        week.days.forEach((day) => {
            if (day.isRestDay) return;

            // Calculate date
            // Week is 1-based in types usually? Let's check. planRepository types says "weekNumber: number".
            // Assuming weekNumber starts at 1.
            const weekOffset = (week.weekNumber - 1) * 7;

            // Day adjustment.
            // If the plan specifies days (0-6), we want to align with those.
            // But we don't know if planStart is Monday or Sunday.
            // Let's assume planStart aligns to the FIRST DAY defined in the plan, or simple day offset.

            // Simple approach:
            // If planStart is a Monday.
            // Week 1, Monday (1) = planStart + (1-1) days.
            // Week 1, Wednesday (3) = planStart + (3-1) days.

            // If we treat planStart as the anchor for "Week 1, Day 0 (Sunday)? Or Monday?"
            // Let's assume planStart is simply the date of "Week 1, Day 1 (if it exists)".
            // BUT PlanDay uses `dayOfWeek` (0-6).

            // Robust approach: Find the start of the week for planStart.
            // We want Sunday to take us back to Sunday week start, or Monday.
            // Let's assume standard ISO week (Monday start) or just respect the Date.

            // Let's project relative to the Sunday of the start week.
            const dayOfStart = planStart.getDay();
            const diffToSunday = dayOfStart; // 0=Sunday.
            const sundayOfStartWeek = new Date(planStart);
            sundayOfStartWeek.setDate(planStart.getDate() - diffToSunday);

            // Target date = SundayOfStartWeek + (Week-1)*7 + DayOfWeek
            const targetDate = new Date(sundayOfStartWeek);
            targetDate.setDate(sundayOfStartWeek.getDate() + weekOffset + day.dayOfWeek);

            // Resolve workout details for duration/name
            let resolvedWorkout = day.workout;
            if (!resolvedWorkout && day.workoutId) {
                resolvedWorkout = getWorkoutById(day.workoutId);
                // Also check custom workouts if not in standard library
                if (!resolvedWorkout) {
                    const customs = getCustomWorkouts();
                    resolvedWorkout = customs.find((w) => w.id === day.workoutId);
                }
            }

            // Start of today for calculation
            const todayStart = new Date();
            todayStart.setHours(0, 0, 0, 0);

            // Check range
            if (targetDate >= rangeStart && targetDate <= rangeEnd) {
                // Determine status
                // We need to check if it's marked complete in planRepository
                // Key format: "week-day" e.g. "1-2" (Week 1, DayOfWeek 2) ??
                // planRepository.markDayComplete uses week, day args.
                // It likely uses the values from loop.
                const isCompleted = isDayComplete(plan.id, week.weekNumber, day.dayOfWeek);
                const duration = resolvedWorkout ? getWorkoutDuration(resolvedWorkout) : 0;
                const title =
                    day.workout?.name || resolvedWorkout?.name || `Week ${week.weekNumber} Day ${day.dayOfWeek}`;

                // Normalize targetDate to ensure correct comparison (it should already be 00:00ish from logic above?)
                // sundayOfStartWeek was derived from planStart. If planStart had time, targetDate has time.
                // Let's strip time from targetDate just in case.
                targetDate.setHours(0, 0, 0, 0);

                let status: CalendarEvent['status'] = 'planned';
                if (isCompleted) {
                    status = 'completed';
                } else if (targetDate < todayStart) {
                    status = 'skipped';
                }

                events.push({
                    id: `plan-${plan.id}-w${week.weekNumber}-d${day.dayOfWeek}`,
                    title,
                    date: targetDate,
                    type: 'plan',
                    status,
                    duration,
                    description: day.notes,
                    planId: plan.id,
                    week: week.weekNumber,
                    day: day.dayOfWeek,
                });
            }
        });
    });

    return events;
}

function isDayComplete(planId: string, week: number, day: number): boolean {
    const progress = planRepository.getActiveProgress();
    if (!progress || progress.planId !== planId) return false;
    return !!progress.completedDays[`${week}-${day}`];
}
