import { TRACKED_DURATIONS } from '../types/personal-records.js';

/**
 * Calculates Mean Maximal Power for various durations in real-time.
 * Maintains rolling averages efficiently using a circular buffer.
 */
export class MeanMaximalPowerCalculator {
    private buffer: Float32Array;
    private maxDuration: number;
    private head: number = 0;
    private totalSamples: number = 0;
    private currentSums: Record<number, number> = {};
    private currentMax: Record<number, number> = {};

    constructor() {
        this.maxDuration = Math.max(...TRACKED_DURATIONS);
        // +1 safety buffer although strict modulo math should work fine
        this.buffer = new Float32Array(this.maxDuration);

        for (const d of TRACKED_DURATIONS) {
            this.currentSums[d] = 0;
            this.currentMax[d] = 0;
        }
    }

    /**
     * Add a new power sample (expected to be 1-second intervals)
     * @param watts Power value in Watts
     * @returns A map of updated max values if any record was broken in this step, or null
     */
    public addSample(watts: number): Record<number, number> | null {
        // Filter invalid
        const val = Math.max(0, isNaN(watts) ? 0 : watts);

        let updates: Record<number, number> | null = null;

        for (const duration of TRACKED_DURATIONS) {
            // Add new value to running sum
            this.currentSums[duration] += val;

            // If window is full, subtract the element leaving the window
            if (this.totalSamples >= duration) {
                const indexToRemove = (this.head - duration + this.maxDuration) % this.maxDuration;
                const valToRemove = this.buffer[indexToRemove];
                this.currentSums[duration] -= valToRemove;
            }

            // If needed samples collected, calculate average
            if (this.totalSamples + 1 >= duration) {
                const avg = this.currentSums[duration] / duration;
                if (avg > this.currentMax[duration]) {
                    this.currentMax[duration] = avg;
                    if (!updates) updates = {};
                    updates[duration] = avg;
                }
            }
        }

        // Store current value in history buffer
        this.buffer[this.head] = val;

        // Move head forward
        this.head = (this.head + 1) % this.maxDuration;
        this.totalSamples++;

        return updates;
    }

    public getCurrentMax(): Record<number, number> {
        return { ...this.currentMax };
    }

    public reset(): void {
        this.totalSamples = 0;
        this.head = 0;
        this.buffer.fill(0);
        for (const d of TRACKED_DURATIONS) {
            this.currentSums[d] = 0;
            this.currentMax[d] = 0;
        }
    }
}
