/**
 * Personal Records and Power Curve Types
 */

/**
 * Represents a single personal record entry
 */
export interface PersonalRecord {
    /** Power value in Watts */
    value: number;
    /** Date when the record was achieved (timestamp) */
    date: number;
    /** Optional identifier for the workout */
    workoutId?: string;
}

/**
 * A collection of personal records indexed by duration (in seconds)
 */
export type PowerCurve = Record<number, PersonalRecord>;

/**
 * Standard durations to track for power curve (in seconds)
 */
export const TRACKED_DURATIONS = [
    1, // 1 sec - Instantaneous
    5, // 5 sec - Sprint
    15, // 15 sec
    30, // 30 sec
    60, // 1 min - Anaerobic
    120, // 2 min
    300, // 5 min - VO2 Max
    600, // 10 min
    1200, // 20 min - FTP Proxy
    3600, // 60 min - FTP
    // 7200    // 2 hours ? Maybe too long for typical usage
] as const;

export type TrackedDuration = (typeof TRACKED_DURATIONS)[number];
