/**
 * Music Controller
 *
 * Integrates MusicEngine with workout state and measurements.
 * Maps power zones to musical intensity and syncs tempo with cadence.
 */

import type { MusicSettings } from '../config/settings.js';
import { MusicEngine } from './MusicEngine.js';

import { getSettings } from '../config/settings.js';
import type { ActiveWorkoutState, WorkoutStep } from '../workouts/types.js';

/**
 * Tempo mapping configuration for cadence sync
 */
interface TempoMapping {
    minCadence: number;
    maxCadence: number;
    minBPM: number;
    maxBPM: number;
}

/**
 * Default tempo mapping for cycling
 */
const CYCLING_TEMPO_MAPPING: TempoMapping = {
    minCadence: 70,
    maxCadence: 110,
    minBPM: 70,
    maxBPM: 110,
};

/**
 * Default tempo mapping for running
 */
const RUNNING_TEMPO_MAPPING: TempoMapping = {
    minCadence: 150, // steps per minute
    maxCadence: 190,
    minBPM: 150,
    maxBPM: 190,
};

/**
 * Manages music integration with workout data
 */
export class MusicController {
    private engine: MusicEngine;
    private settings: MusicSettings;
    private currentPower: number = 0;
    private currentHeartRate: number = 0;
    private currentCadence: number = 0;
    private ftpWatts: number = 0;
    private maxHeartRate: number = 0;
    private tempoMapping: TempoMapping = CYCLING_TEMPO_MAPPING;
    private intensitySmoothing: number = 0.05; // Lower = smoother transitions
    private lastStepIndex: number = -1;

    constructor(settings: MusicSettings) {
        this.settings = { ...settings };
        this.engine = new MusicEngine(settings);
    }

    /**
     * Initialize the music engine
     */
    async initialize(): Promise<void> {
        await this.engine.initialize();
    }

    /**
     * Start music playback
     */
    async start(): Promise<void> {
        if (!this.settings.enabled) {
            console.log('[MusicController] Music is disabled in settings');
            return;
        }

        await this.engine.start();
    }

    /**
     * Stop music playback
     */
    stop(): void {
        this.engine.stop();
    }

    /**
     * Update with current measurements
     *
     * @param measurements - Current workout measurements
     */
    updateMeasurements(measurements: {
        power?: number;
        heartRate?: number;
        cadence?: number;
    }): void {
        if (measurements.power !== undefined) {
            this.currentPower = measurements.power;
        }
        if (measurements.heartRate !== undefined) {
            this.currentHeartRate = measurements.heartRate;
        }
        if (measurements.cadence !== undefined) {
            this.currentCadence = measurements.cadence;
        }

        // Update intensity based on current settings
        this.updateIntensity();

        // Update tempo if synced to cadence
        if (this.settings.tempoSync === 'cadence' && this.currentCadence > 0) {
            this.updateTempo();
        }
    }

    /**
     * Update musical intensity based on current intensity mapping
     */
    private updateIntensity(): void {
        let targetIntensity = 0.5;

        switch (this.settings.intensityMapping) {
            case 'power':
                targetIntensity = this.calculatePowerIntensity();
                break;
            case 'heartrate':
                targetIntensity = this.calculateHeartRateIntensity();
                break;
            case 'manual':
                // Manual intensity - don't auto-update
                return;
        }

        // Apply smoothing for gradual transitions
        const currentIntensity = this.engine.getIntensity();
        const smoothedIntensity =
            currentIntensity + (targetIntensity - currentIntensity) * this.intensitySmoothing;

        this.engine.setIntensity(smoothedIntensity);
    }

    /**
     * Calculate intensity from power (0-1)
     * Maps to 5 power zones:
     * Zone 1: 0-0.2
     * Zone 2: 0.2-0.4
     * Zone 3: 0.4-0.6
     * Zone 4: 0.6-0.8
     * Zone 5: 0.8-1.0
     */
    private calculatePowerIntensity(): number {
        if (!this.ftpWatts || this.currentPower <= 0) {
            return 0.5; // Default to medium intensity
        }

        const percentFTP = this.currentPower / this.ftpWatts;
        // Clamp to reasonable range (0-200% FTP)
        const clamped = Math.max(0, Math.min(2, percentFTP));
        // Map to 0-1 intensity
        return clamped / 2;
    }

    /**
     * Calculate intensity from heart rate (0-1)
     */
    private calculateHeartRateIntensity(): number {
        if (!this.maxHeartRate || this.currentHeartRate <= 0) {
            return 0.5; // Default to medium intensity
        }

        const percentMax = this.currentHeartRate / this.maxHeartRate;
        // Clamp to reasonable range (40-100% max HR)
        const clamped = Math.max(0.4, Math.min(1, percentMax));
        // Map to 0-1 intensity (scale from 40% to 100% HR)
        return (clamped - 0.4) / 0.6;
    }

    /**
     * Update tempo based on current cadence
     * TODO: Implement time-stretching for actual tempo changes
     */
    private updateTempo(): void {
        const bpm = this.cadenceToBPM(this.currentCadence);
        // For now, just log - actual tempo change requires time-stretching
        // This would be implemented in Phase 2
        console.debug(`[MusicController] Target BPM: ${bpm} (from cadence ${this.currentCadence})`);
    }

    /**
     * Convert cadence to BPM using current tempo mapping
     */
    private cadenceToBPM(cadence: number): number {
        const { minCadence, maxCadence, minBPM, maxBPM } = this.tempoMapping;
        const normalized = (cadence - minCadence) / (maxCadence - minCadence);
        const clamped = Math.max(0, Math.min(1, normalized));
        return minBPM + clamped * (maxBPM - minBPM);
    }

    /**
     * Set FTP (Functional Threshold Power) for power zone calculations
     *
     * @param watts - FTP in watts
     */
    setFTP(watts: number): void {
        this.ftpWatts = watts;
    }

    /**
     * Set max heart rate for HR zone calculations
     *
     * @param bpm - Max heart rate in BPM
     */
    setMaxHeartRate(bpm: number): void {
        this.maxHeartRate = bpm;
    }

    /**
     * Set tempo mapping (cycling vs running)
     *
     * @param mapping - Tempo mapping configuration
     */
    setTempoMapping(mapping: TempoMapping): void {
        this.tempoMapping = mapping;
    }

    /**
     * Use cycling tempo mapping
     */
    useCyclingTempo(): void {
        this.setTempoMapping(CYCLING_TEMPO_MAPPING);
    }

    /**
     * Use running tempo mapping
     */
    useRunningTempo(): void {
        this.setTempoMapping(RUNNING_TEMPO_MAPPING);
    }

    /**
     * Manually set intensity (overrides automatic mapping)
     *
     * @param intensity - Intensity value (0-1)
     */
    setManualIntensity(intensity: number): void {
        this.engine.setIntensity(intensity);
    }

    private estimateStepPower(step: WorkoutStep | null): number {
        if (!step) return 0;

        // Handle explicit target
        if (step.target) {
            const val = step.target.value ?? ((step.target.min || 0) + (step.target.max || 0)) / 2;
            if (step.target.type === 'power') {
                if (step.target.unit === 'watts') return val;
                if (step.target.unit === 'percent_ftp') return (val / 100) * (this.ftpWatts || 200);
            }
        }

        // Handle ramp
        if (step.rampStart && step.rampEnd) {
            const startVal = step.rampStart.value ?? 0;
            const endVal = step.rampEnd.value ?? 0;
            const avg = (startVal + endVal) / 2;
            if (step.rampStart.type === 'power') {
                if (step.rampStart.unit === 'watts') return avg;
                if (step.rampStart.unit === 'percent_ftp') return (avg / 100) * (this.ftpWatts || 200);
            }
        }

        return 0;
    }

    /**
     * Handle workout state changes (steps, intervals)
     */
    onWorkoutStateChange(state: ActiveWorkoutState): void {
        const { currentStep, nextStep, stepTimeRemaining, currentStepIndex } = state;

        // Detect step change
        if (currentStepIndex !== this.lastStepIndex) {
            this.lastStepIndex = currentStepIndex;
            // New interval started
            console.log(`[MusicController] Step changed to ${currentStep.type}`);

            // Cooldown/drop when entering recovery or low intensity
            const estimatedPower = this.estimateStepPower(currentStep);
            const isRecovery = currentStep.type === 'recovery' ||
                (estimatedPower > 0 && this.ftpWatts > 0 && estimatedPower < this.ftpWatts * 0.55);

            if (isRecovery) {
                this.engine.triggerCooldown();
            }
        }

        // Build up music before high intensity interval
        if (nextStep && stepTimeRemaining <= 10 && stepTimeRemaining >= 9) {
            // 10 seconds before next step
            const currentTarget = this.estimateStepPower(currentStep);
            const nextTarget = this.estimateStepPower(nextStep);

            // If intensity is increasing significantly (e.g. > 20% increase)
            if (nextTarget > currentTarget * 1.2) {
                console.log('[MusicController] Triggering buildup');
                this.engine.triggerBuildup(Math.max(1000, stepTimeRemaining * 1000));
            }
        }
    }

    /**
     * Duck music volume (for voice announcements)
     *
     * @param durationMs - Optional duration. If not provided, stays ducked until unducked.
     */
    duck(durationMs?: number): void {
        if (this.settings.duckingEnabled) {
            this.engine.duck(this.settings.duckingLevel, durationMs);
        }
    }

    /**
     * Restore music volume
     */
    unduck(): void {
        this.engine.unduck();
    }


    /**
     * Update settings
     *
     * @param newSettings - Partial settings to update
     */
    updateSettings(newSettings: Partial<MusicSettings>): void {
        const oldStyle = this.settings.style;
        this.settings = { ...this.settings, ...newSettings };
        this.engine.updateSettings(newSettings);

        // Handle style change
        if (newSettings.style !== undefined && newSettings.style !== oldStyle) {
            this.engine.setStyle(newSettings.style).catch((err) => {
                console.error('[MusicController] Failed to switch style:', err);
            });
        }

        // Handle enabled state change
        if (newSettings.enabled !== undefined) {
            if (newSettings.enabled && !this.engine.isPlaying()) {
                this.start().catch((error) => {
                    console.error('[MusicController] Failed to start:', error);
                });
            } else if (!newSettings.enabled && this.engine.isPlaying()) {
                this.stop();
            }
        }
    }

    /**
     * Check if music is currently playing
     */
    isPlaying(): boolean {
        return this.engine.isPlaying();
    }

    /**
     * Get current intensity
     */
    getIntensity(): number {
        return this.engine.getIntensity();
    }

    /**
     * Register event listener on the engine
     */
    on(event: 'started' | 'stopped' | 'intensityChanged' | 'error', listener: (data?: unknown) => void): void {
        this.engine.on(event, listener);
    }

    /**
     * Unregister event listener from the engine
     */
    off(event: 'started' | 'stopped' | 'intensityChanged' | 'error', listener: (data?: unknown) => void): void {
        this.engine.off(event, listener);
    }

    /**
     * Clean up resources
     */
    dispose(): void {
        this.engine.dispose();
    }
}

// Singleton instance
export const musicController = new MusicController(getSettings().music);
