import { MeasurementsState } from '../measurements-state.js';
import type { ConnectionsState } from '../types/connections.js';
import { settingsStore, type SettingsStore } from './SettingsStore.js';
import type { TimeState } from '../getInitState.js';

export interface AppState {
    measurementsState: MeasurementsState;
    connectionsState: ConnectionsState;
    timeState: TimeState;
    settings: SettingsStore;
}

let appState: AppState | null = null;

export function initializeState(): AppState {
    if (appState) {
        console.warn('State already initialized');
        return appState;
    }

    appState = {
        measurementsState: new MeasurementsState(),
        connectionsState: {
            power: { isConnected: false, disconnect: null },
            heartrate: { isConnected: false, disconnect: null },
            cadence: { isConnected: false, disconnect: null },
            speed: { isConnected: false, disconnect: null },
            gps: { isConnected: false, disconnect: null },
            treadmill: { isConnected: false, disconnect: null },
        },
        timeState: {
            running: false,
            startTime: null,
            endTime: null,
        },
        // In the plan it says 'typeof settingsStore' but that refers to the class generally in TS if used as type,
        // here we want the instance type. "typeof settingsStore" matches the instance variable type.
        // Actually, let's just use the instance directly.
        settings: settingsStore,
    };

    // Initialize settings
    settingsStore.initialize();

    return appState;
}

export function getState(): AppState {
    if (!appState) {
        throw new Error('State not initialized. Call initializeState() first.');
    }
    return appState;
}

// For testing only
export function resetState(): void {
    appState = null;
}

// Development/testing helpers (only available in dev mode)
if (import.meta.env.DEV) {
    (window as any).__getAppState = getState;
    (window as any).__resetState = resetState;
}
