
/**
 * Audio Asset Generator
 * 
 * Generates audio buffers procedurally at runtime to avoid shipping large WAV files.
 * Replicates the logic previously used in offline generation scripts.
 */

export type GeneratorType = 'bass' | 'drums-low' | 'drums-high' | 'synth';

export class AudioGenerator {
    private context: AudioContext;
    private sampleRate: number;
    private bpm: number = 120;
    private bars: number = 4;

    constructor(context: AudioContext) {
        this.context = context;
        this.sampleRate = context.sampleRate;
    }

    /**
     * Generate an audio buffer for a specific type and style
     */
    generate(type: string, style: string = 'electronic'): AudioBuffer {
        const beatDuration = 60 / this.bpm;
        const totalDuration = beatDuration * 4 * this.bars; // 4 beats * 4 bars
        const length = Math.floor(this.sampleRate * totalDuration);

        const buffer = this.context.createBuffer(1, length, this.sampleRate);
        const data = buffer.getChannelData(0);

        switch (type) {
            case 'bass':
                this.generateBass(data, length, beatDuration, style);
                break;
            case 'drums-low':
                this.generateDrumsLow(data, length, beatDuration, style);
                break;
            case 'drums-high':
                this.generateDrumsHigh(data, length, beatDuration, style);
                break;
            case 'synth':
                this.generateSynth(data, length, beatDuration, style);
                break;
            default:
                // Silent buffer if unknown
                break;
        }

        return buffer;
    }

    private generateBass(data: Float32Array, length: number, beatDuration: number, style: string): void {
        for (let i = 0; i < length; i++) {
            const time = i / this.sampleRate;
            const beat = (time % beatDuration) / beatDuration;

            // Simple bass sine wave
            const envelope = 1 - beat; // Decay
            const freq = style === 'ambient' ? 45 : 55; // Lower for ambient
            const value = Math.sin(time * freq * 2 * Math.PI) * envelope * 0.6;

            // Add some grit for rock
            if (style === 'rock') {
                data[i] = Math.max(-1, Math.min(1, value + (Math.random() * 0.1)));
            } else {
                data[i] = value;
            }
        }
    }

    private generateDrumsLow(data: Float32Array, length: number, beatDuration: number, style: string): void {
        for (let i = 0; i < length; i++) {
            const time = i / this.sampleRate;
            const beatTime = time % beatDuration;

            // Kick drum on every beat
            let value = 0;

            if (beatTime < 0.2) {
                const t = beatTime / 0.2;
                const freq = 150 * (1 - t);
                const env = 1 - t;
                value = Math.sin(beatTime * freq * 2 * Math.PI) * env;
            }

            if (style === 'minimal' && i % 2 !== 0) {
                // Skip every other beat roughly? No, i is sample.
                // Minimal: Kick only on 1 and 3?
                // Current logic is every beat. Let's keep it simple.
            }

            data[i] = value;
        }
    }

    private generateDrumsHigh(data: Float32Array, length: number, beatDuration: number, style: string): void {
        for (let i = 0; i < length; i++) {
            const time = i / this.sampleRate;
            const beatTime = time % beatDuration;

            let value = 0;

            // Hi-hat on off-beats
            if (beatTime > (beatDuration / 2) && beatTime < (beatDuration / 2) + 0.1) {
                // White noise
                value = (Math.random() * 2 - 1) * 0.3;
            }

            if (style === 'rock') {
                // Add "snare" roughly on beat 2 and 4 (simple approximation)
                // This logic is per beat, so it's repeating every beat.
                // We'd need bar context for proper backbeats.
                // For now, keep the simple loop.
                value *= 1.5; // Louder
            }

            data[i] = value;
        }
    }

    private generateSynth(data: Float32Array, length: number, beatDuration: number, style: string): void {
        for (let i = 0; i < length; i++) {
            const time = i / this.sampleRate;

            // Simple arpeggio 
            const notes = [261.63, 329.63, 392.00, 523.25]; // C, E, G, C(octave)
            const noteDuration = beatDuration / 4; // 16th notes
            const currentNoteIdx = Math.floor(time / noteDuration) % 4;
            let freq = notes[currentNoteIdx];

            if (style === 'ambient') {
                freq /= 2; // Lower octave
            } else if (style === 'minimal') {
                freq = notes[0]; // Just C constant pulse
            }

            const value = Math.sin(time * freq * 2 * Math.PI) * 0.3;

            data[i] = value;
        }
    }
}
