import type { StructuredWorkout, WorkoutStep, TargetType, TargetUnit } from '../types.js';

interface DataPoint {
    timeMinutes: number;
    value: number;
}

export function parseErgMrc(content: string, fileName: string): StructuredWorkout {
    const lines = content.split(/\r?\n/);
    let isHeader = false;
    let isData = false;
    let mode: 'percent' | 'watts' | null = null;
    let description = '';

    const points: DataPoint[] = [];

    for (const line of lines) {
        const trimmed = line.trim();
        if (!trimmed) continue;

        if (trimmed.toUpperCase() === '[COURSE HEADER]') {
            isHeader = true;
            isData = false;
            continue;
        }

        if (trimmed.toUpperCase() === '[END COURSE HEADER]') {
            isHeader = false;
            continue;
        }

        if (trimmed.toUpperCase() === '[COURSE DATA]') {
            isHeader = false;
            isData = true;
            continue;
        }

        if (trimmed.toUpperCase() === '[END COURSE DATA]') {
            isData = false;
            continue;
        }

        if (isHeader) {
            if (trimmed.toUpperCase().includes('MINUTES PERCENT')) {
                mode = 'percent';
            } else if (trimmed.toUpperCase().includes('MINUTES WATTS')) {
                mode = 'watts';
            } else {
                const parts = trimmed.split('=');
                if (parts.length === 2) {
                    const key = parts[0].trim().toUpperCase();
                    const val = parts[1].trim();
                    if (key === 'DESCRIPTION') description = val;
                }
            }
        }

        if (isData) {
            // Split by tab or space
            const parts = trimmed.split(/[\s\t]+/);
            if (parts.length >= 2) {
                const t = parseFloat(parts[0]);
                const v = parseFloat(parts[1]);
                if (!isNaN(t) && !isNaN(v)) {
                    points.push({ timeMinutes: t, value: v });
                }
            }
        }
    }

    if (!mode) {
        // Fallback detection if header missing
        if (fileName.toLowerCase().endsWith('.erg')) mode = 'watts';
        else mode = 'percent';
    }

    const steps: WorkoutStep[] = [];

    // Process points into steps
    for (let i = 0; i < points.length - 1; i++) {
        const current = points[i];
        const next = points[i + 1];

        const durationMinutes = next.timeMinutes - current.timeMinutes;

        // Skip zero-duration segments (instant jumps)
        if (durationMinutes <= 0.0001) continue;

        const durationSeconds = Math.round(durationMinutes * 60);

        const typeStr = mode === 'percent' ? 'percent_ftp' : 'watts';
        const unit: TargetUnit = typeStr;
        const targetType: TargetType = 'power';

        const step: WorkoutStep = {
            type: 'active', // Default to active
            duration: durationSeconds,
        };

        // Determine if steady state or ramp
        if (Math.abs(current.value - next.value) < 0.1) {
            // Steady state
            step.target = {
                type: targetType,
                unit: unit,
                value: current.value,
            };
            step.description = `${current.value} ${unit === 'percent_ftp' ? '%' : 'W'}`;
        } else {
            // Ramp
            step.rampStart = {
                type: targetType,
                unit: unit,
                value: current.value,
            };
            step.rampEnd = {
                type: targetType,
                unit: unit,
                value: next.value,
            };
            step.description = `Ramp ${current.value} -> ${next.value} ${unit === 'percent_ftp' ? '%' : 'W'}`;
        }

        // Simple logic to guess interval type
        if (unit === 'percent_ftp') {
            const avgVal = (current.value + next.value) / 2;
            if (avgVal < 55) step.type = 'recovery';
            else if (avgVal < 76)
                step.type = 'active'; // Endurance
            else step.type = 'active';

            // First step often warmup
            if (i === 0 && avgVal < 75) step.type = 'warmup';
            // Last step often cooldown
            if (i >= points.length - 2 && avgVal < 60) step.type = 'cooldown';
        }

        steps.push(step);
    }

    // Attempt to clean name from filename if description is empty or generic
    const workoutName = description || fileName.replace(/\.[^/.]+$/, '').replace(/_/g, ' ');

    return {
        id: crypto.randomUUID(),
        name: workoutName,
        description: description,
        steps: steps,
        tags: ['imported', mode === 'percent' ? 'mrc' : 'erg'],
    };
}
