/**
 * Component Lazy Loader
 *
 * Facilitates lazy loading of Web Components when they appear in the DOM.
 * This avoids loading unused components and reduces initial bundle size.
 */

type ComponentLoader = () => Promise<unknown>;

const registry = new Map<string, ComponentLoader>();
const loaded = new Set<string>();
let observer: MutationObserver | null = null;

/**
 * Register a component to be lazy loaded when it appears in the DOM.
 *
 * @param tagName - The custom element tag name (e.g., 'bpt-profile-settings')
 * @param loader - Function that imports the component module
 */
export function registerLazyComponent(tagName: string, loader: ComponentLoader): void {
    const upperTag = tagName.toUpperCase();

    if (customElements.get(tagName)) {
        console.warn(`Component ${tagName} is already defined.`);
        return;
    }

    registry.set(upperTag, loader);

    // Start observer if not running
    if (!observer) {
        startObserver();
    }

    // Check if component is already in the DOM
    checkForComponent(upperTag);
}

function startObserver() {
    observer = new MutationObserver((mutations) => {
        // Optimization: check if any nodes were actually added
        let nodesAdded = false;
        for (const m of mutations) {
            if (m.addedNodes.length > 0) {
                nodesAdded = true;
                break;
            }
        }

        if (nodesAdded) {
            checkAllPending();
        }
    });

    observer.observe(document.body, { childList: true, subtree: true });
}

function checkAllPending() {
    for (const tagName of registry.keys()) {
        if (!loaded.has(tagName)) {
            checkForComponent(tagName);
        }
    }
}

function checkForComponent(tagName: string) {
    // If we find the element in the DOM, trigger load
    if (document.querySelector(tagName)) {
        loadComponent(tagName);
    }
}

async function loadComponent(tagName: string) {
    if (loaded.has(tagName)) return;
    loaded.add(tagName);

    const loader = registry.get(tagName);
    if (!loader) return;

    try {
        await loader();
        // The component module should call customElements.define()
    } catch (error) {
        console.error(`Failed to lazy load component ${tagName}:`, error);
        loaded.delete(tagName); // Allow retry?
    }
}
