/**
 * Logic for Personal Records view in History tab
 */
import { personalRecordsStore } from '../state/PersonalRecordsStore.js';
import type { PersonalRecord } from '../types/personal-records.js';
// Using Chart from window/global instead of import to reuse existing library (it might be loaded globally or similar)
// If not, we should use the same import method as charts.ts.
// Let's check charts.ts to see how it imports Chart.js
import { Chart, type ChartConfiguration } from 'chart.js/auto';

let pcChart: Chart | null = null;
let initialized = false;

export function initPrView() {
    if (initialized) return;

    personalRecordsStore.subscribe(() => {
        refreshPrView();
    });

    initialized = true;
}

export function refreshPrView() {
    renderPrCards();
    renderPowerCurve();
}

/**
 * Render PR Cards (All-Time and Season)
 */
function renderPrCards() {
    const allTimeContainer = document.getElementById('prListAllTime');
    const seasonContainer = document.getElementById('prListSeason');

    if (!allTimeContainer || !seasonContainer) return;

    const allTime = personalRecordsStore.getAllTimeCurve();
    const season = personalRecordsStore.getSeasonCurve();

    allTimeContainer.innerHTML = generatePrGridHtml(allTime);
    seasonContainer.innerHTML = generatePrGridHtml(season);
}

function generatePrGridHtml(curve: Record<number, PersonalRecord>): string {
    const durations = Object.keys(curve)
        .map(Number)
        .sort((a, b) => a - b);

    if (durations.length === 0) {
        return '<div style="grid-column: 1/-1; color: var(--color-text-secondary); text-align: center; padding: 20px;">No records yet</div>';
    }

    return durations
        .map((d) => {
            const record = curve[d];
            const label = formatDurationLabel(d);
            const dateStr = new Date(record.date).toLocaleDateString();

            return `
            <div class="pr-card" style="
                background: var(--card-bg); 
                padding: 12px; 
                border-radius: 8px; 
                border: 1px solid var(--color-border);
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 4px;
            ">
                <span style="font-size: 0.9em; color: var(--color-text-secondary);">${label}</span>
                <span style="font-size: 1.4em; font-weight: bold; color: var(--color-accent);">${Math.round(record.value)}W</span>
                <span style="font-size: 0.75em; color: var(--color-text-muted);">${dateStr}</span>
            </div>
        `;
        })
        .join('');
}

function formatDurationLabel(seconds: number): string {
    if (seconds < 60) return `${seconds}s`;
    return `${Math.floor(seconds / 60)}m`;
}

/**
 * Render Power Curve Chart
 */
function renderPowerCurve() {
    const canvas = document.getElementById('powerCurveHistoryChart') as HTMLCanvasElement;
    if (!canvas) return;

    const allTime = personalRecordsStore.getAllTimeCurve();
    const season = personalRecordsStore.getSeasonCurve();

    // Sort durations
    const durations = Array.from(
        new Set([...Object.keys(allTime).map(Number), ...Object.keys(season).map(Number)])
    ).sort((a, b) => a - b);

    if (durations.length === 0) return;

    const labels = durations.map(formatDurationLabel);
    const allTimeData = durations.map((d) => allTime[d]?.value || null);
    const seasonData = durations.map((d) => season[d]?.value || null);

    if (pcChart) {
        pcChart.destroy();
    }

    const config: ChartConfiguration = {
        type: 'line',
        data: {
            labels,
            datasets: [
                {
                    label: 'All-Time Best',
                    data: allTimeData,
                    borderColor: '#f59e0b', // Amber-500
                    backgroundColor: 'rgba(245, 158, 11, 0.1)',
                    borderWidth: 2,
                    tension: 0.3,
                    pointRadius: 4,
                    fill: false,
                },
                {
                    label: 'Season Best',
                    data: seasonData,
                    borderColor: '#3b82f6', // Blue-500
                    backgroundColor: 'rgba(59, 130, 246, 0.1)',
                    borderWidth: 2,
                    tension: 0.3,
                    pointRadius: 4,
                    fill: true,
                },
            ],
        },
        options: {
            responsive: true,
            maintainAspectRatio: false,
            interaction: {
                mode: 'index',
                intersect: false,
            },
            plugins: {
                legend: {
                    position: 'top',
                    labels: { color: getComputedStyle(document.body).getPropertyValue('--color-text-primary') },
                },
                tooltip: {
                    callbacks: {
                        label: (ctx: any) => `${ctx.dataset.label}: ${Math.round(ctx.parsed.y)}W`,
                    },
                },
            },
            scales: {
                x: {
                    grid: { color: 'rgba(128, 128, 128, 0.1)' },
                    ticks: { color: getComputedStyle(document.body).getPropertyValue('--color-text-secondary') },
                },
                y: {
                    beginAtZero: true,
                    title: { display: true, text: 'Power (Watts)' },
                    grid: { color: 'rgba(128, 128, 128, 0.1)' },
                    ticks: { color: getComputedStyle(document.body).getPropertyValue('--color-text-secondary') },
                },
            },
        },
    };

    pcChart = new Chart(canvas, config);
}
