/**
 * Feature Flag Service
 *
 * Manages feature flags for runtime control of application features.
 * Persists user preferences to localStorage and provides event-based
 * notification when flags change.
 *
 * @module FeatureFlagService
 */

import {
    DEFAULT_FLAGS,
    FEATURE_FLAGS_STORAGE_KEY,
    type FeatureFlag,
    type FeatureFlagConfig,
} from '../config/feature-flags.js';

type ChangeListener = (flag: FeatureFlag) => void;

/**
 * Service that manages feature flags with localStorage persistence
 */
export class FeatureFlagServiceImpl {
    private flags: FeatureFlagConfig;
    private listeners: Set<ChangeListener> = new Set();

    constructor() {
        this.flags = this.loadFlags();
    }

    /**
     * Load flags from localStorage, merging with defaults
     */
    private loadFlags(): FeatureFlagConfig {
        const flags: FeatureFlagConfig = {};

        // Start with defaults
        for (const [key, flag] of Object.entries(DEFAULT_FLAGS)) {
            flags[key] = { ...flag };
        }

        // Load overrides from localStorage
        try {
            const stored = localStorage.getItem(FEATURE_FLAGS_STORAGE_KEY);
            if (stored) {
                const overrides: Record<string, boolean> = JSON.parse(stored);
                for (const [key, enabled] of Object.entries(overrides)) {
                    if (flags[key]) {
                        flags[key].enabled = enabled;
                    }
                }
            }
        } catch (error) {
            console.error('[FeatureFlags] Failed to load from localStorage:', error);
        }

        return flags;
    }

    /**
     * Save current flag overrides to localStorage
     */
    private saveFlags(): void {
        const overrides: Record<string, boolean> = {};

        for (const [key, flag] of Object.entries(this.flags)) {
            // Only store overrides (different from default)
            const defaultFlag = DEFAULT_FLAGS[key];
            if (defaultFlag && flag.enabled !== defaultFlag.enabled) {
                overrides[key] = flag.enabled;
            }
        }

        try {
            if (Object.keys(overrides).length > 0) {
                localStorage.setItem(FEATURE_FLAGS_STORAGE_KEY, JSON.stringify(overrides));
            } else {
                localStorage.removeItem(FEATURE_FLAGS_STORAGE_KEY);
            }
        } catch (error) {
            console.error('[FeatureFlags] Failed to save to localStorage:', error);
        }
    }

    /**
     * Check if a feature flag is enabled
     */
    isEnabled(flagKey: string): boolean {
        const flag = this.flags[flagKey];
        if (!flag) {
            console.warn(`[FeatureFlags] Unknown flag: ${flagKey}`);
            return false;
        }
        return flag.enabled;
    }

    /**
     * Enable a feature flag
     */
    enable(flagKey: string): void {
        const flag = this.flags[flagKey];
        if (!flag) {
            console.warn(`[FeatureFlags] Cannot enable unknown flag: ${flagKey}`);
            return;
        }

        if (!flag.enabled) {
            flag.enabled = true;
            this.saveFlags();
            this.notifyListeners(flag);
            console.log(`[FeatureFlags] Enabled: ${flagKey}`);
        }
    }

    /**
     * Disable a feature flag
     */
    disable(flagKey: string): void {
        const flag = this.flags[flagKey];
        if (!flag) {
            console.warn(`[FeatureFlags] Cannot disable unknown flag: ${flagKey}`);
            return;
        }

        if (flag.enabled) {
            flag.enabled = false;
            this.saveFlags();
            this.notifyListeners(flag);
            console.log(`[FeatureFlags] Disabled: ${flagKey}`);
        }
    }

    /**
     * Toggle a feature flag
     */
    toggle(flagKey: string): void {
        if (this.isEnabled(flagKey)) {
            this.disable(flagKey);
        } else {
            this.enable(flagKey);
        }
    }

    /**
     * Get a feature flag by key
     */
    getFlag(flagKey: string): FeatureFlag | undefined {
        return this.flags[flagKey];
    }

    /**
     * Get all feature flags (returns a deep copy)
     */
    getAllFlags(): FeatureFlagConfig {
        const copy: FeatureFlagConfig = {};
        for (const [key, flag] of Object.entries(this.flags)) {
            copy[key] = { ...flag };
        }
        return copy;
    }

    /**
     * Reset all flags to their default values
     */
    resetToDefaults(): void {
        this.flags = {};
        for (const [key, flag] of Object.entries(DEFAULT_FLAGS)) {
            this.flags[key] = { ...flag };
        }

        try {
            localStorage.removeItem(FEATURE_FLAGS_STORAGE_KEY);
        } catch (error) {
            console.error('[FeatureFlags] Failed to clear localStorage:', error);
        }

        // Notify listeners for all flags
        for (const flag of Object.values(this.flags)) {
            this.notifyListeners(flag);
        }

        console.log('[FeatureFlags] Reset to defaults');
    }

    /**
     * Subscribe to flag changes
     */
    on(event: 'change', callback: ChangeListener): void {
        if (event === 'change') {
            this.listeners.add(callback);
        }
    }

    /**
     * Unsubscribe from flag changes
     */
    off(event: 'change', callback: ChangeListener): void {
        if (event === 'change') {
            this.listeners.delete(callback);
        }
    }

    /**
     * Notify all listeners of a flag change
     */
    private notifyListeners(flag: FeatureFlag): void {
        for (const listener of this.listeners) {
            try {
                listener(flag);
            } catch (error) {
                console.error('[FeatureFlags] Listener error:', error);
            }
        }
    }
}

/**
 * Singleton instance of the feature flag service
 */
export const featureFlagService = new FeatureFlagServiceImpl();
